<template>
  <div class="hostservice-netfilter-data" style="width: 100%;padding: 8px;">
    <g-el-table
      :tableHeadShow="tableHeadShow"
      :url="url"
      :type="type"
      :cloumns="cloumns"
      :params="params"
      :pageParams="pageParams"
      @selectChange="selectChange"
      ref="hostservice_netfilter_data"
    >
      <template v-slot:table_header>
        <el-button type="danger" size="mini" @click="delAll(selectData)">批量删除</el-button>
        <el-button type="primary" size="mini" @click="generate(selectData)">批量生成</el-button>
        <el-button type="primary" size="mini" @click="refresh">刷新</el-button>
      </template>
      <template v-slot:operation="props">
        <div v-if="props.data.row.status == 0">
          <el-button type="primary" size="mini" @click="generate_rule(props.data.row)">生成规则</el-button>
        </div>
        <div v-if="props.data.row.status == 1">
          <el-button type="danger" size="mini" >已生成规则</el-button>
        </div>
      </template>
      
      <!-- 来源地址 -->
      <template v-slot:src_ip="props">
        <el-input
          v-model="params.src_ip"
          size="mini"
          placeholder="请输入关键字"
          @keyup.enter.native="search"
          :maxLength="20"
        ></el-input>
      </template>

      <!-- 流量方向 -->
      <template v-slot:direction="props">
        <div v-if="props.data.row.direction == 1">
          <span v-html="icon.directionIcon.outbound"></span>出站
        </div>
        <div v-else-if="props.data.row.direction == 2">
          <span v-html="icon.directionIcon.inbound"></span>入站
        </div>
      </template>

      <template v-slot:direction_head="props">
        <el-select v-model="params.direction" clearable @change="search" size="mini" placeholder="请选择">
          <el-option
            v-for="item in select.direction"
            :key="item.id"
            :label="item.name"
            :value="item.id"
          ></el-option>
        </el-select>
      </template>

      <!-- 目标地址 -->
      <template v-slot:dest_ip="props">
        <el-input
          v-model="params.dest_ip"
          size="mini"
          placeholder="请输入关键字"
          @keyup.enter.native="search"
          :maxLength="20"
        ></el-input>
      </template>

      <!-- 目标端口 -->
      <template v-slot:dest_port="props">
        <el-input
          v-model="params.dest_port"
          size="mini"
          placeholder="请输入关键字"
          @keyup.enter.native="search"
          :maxLength="20"
        ></el-input>
      </template>

      <!-- 协议 -->
      <template slot="protocols" slot-scope="props">
        <div v-if="props.data.row.protocols == 1">TCP</div>
        <div v-else-if="props.data.row.protocols == 2">UDP</div>
        <div v-else-if="props.data.row.protocols == 3">All</div>
      </template>
      <template v-slot:protocols_head="props">
        <el-select v-model="params.protocols" clearable @change="search" size="mini" placeholder="请选择">
          <el-option
            v-for="item in select.protocols"
            :key="item.id"
            :label="item.name"
            :value="item.id"
          ></el-option>
        </el-select>
      </template>

      <!-- 访问次数 -->
      <template slot="view" slot-scope="props">
        <div v-html="props.data.row.view"></div>
      </template>

      <!-- 是否生成 -->
      <template slot="status" slot-scope="props">
        <div v-if="props.data.row.status == 1">
          <span v-html="icon.stateIcon.success"></span>已生成
        </div>
        <div v-else-if="props.data.row.status == 0">
          <span v-html="icon.stateIcon.error"></span>未生成
        </div>
      </template>
      <template v-slot:status_head="props">
        <el-select v-model="params.status" clearable @change="search" size="mini" placeholder="请选择">
          <el-option
            v-for="item in select.is_generate"
            :key="item.id"
            :label="item.name"
            :value="item.id"
          ></el-option>
        </el-select>
      </template>

      <!-- 学习时间 -->
      <template slot="ctime" slot-scope="props">
        <div v-html="props.ctime"></div>
      </template>
      <template v-slot:ctime_head="props">
        <el-date-picker
          type="date"
          size="mini"
          placeholder="选择日期"
          value-format="yyyy-MM-dd"
          v-model="params.ctime"
          @change="search"
          style="width: 100%;"
        ></el-date-picker>
      </template>

    </g-el-table>
  </div>
</template>
<script>
export default {
  name: "hostservice_netfilter_data",
  data() {
    return {
      url:
        "/host/" +
        this.host_id +
        "/netfilter/learn/data/" +
        this.mark +
        "/list",
      type: "post",
      steduytime: "",
      cloumns: [
        {
          type: "selection",
          fixed: "left"
        },
        {
          type: "index",
          // fixed: "left",
          label: "序号",
          width: "60px",
          second_head: {
            type: "index",
            width: "60px",
            label: " ",
            indexMethod: function(index) {
              return index + 1;
            }
          }
        },
        {
          label: "来源地址",
          sortable: true,
          prop: "src_ip",
          sortMethod: this.$tools.src_ipSort,
          second_head: {
            prop: "src_ip",
            templateHeadName: "src_ip",
            width: "150px"
          }
        },
        {
          label: "流量方向",
          sortable: true,
          prop: "direction",
          second_head: {
            isTemplate: true,
            templateName: "direction",
            templateHeadName: "direction_head",
            width: "130px"
          }
        },
        {
          label: "目标地址",
          prop: "dest_ip",
          sortMethod: this.$tools.dest_ipSort,
          sortable: true,
          second_head: {
            templateHeadName: "dest_ip",
            width: "150px",
            prop: "dest_ip"
          }
        },
        {
          label: "目标端口",
          prop: "dest_port",
          sortable: true,
          second_head: {
            templateHeadName: "dest_port",
            width: "150px",
            prop: "dest_port"
          }
        },
        {
          label: "协议",
          prop: "protocols",
          sortable: true,
          second_head: {
            isTemplate: true,
            templateName: "protocols",
            templateHeadName: "protocols_head",
            prop: "protocols",
            width: "130px",
          }
        },
        {
          label: "访问次数",
          prop: "view",
          isTemplate: true,
          templateName: "view",
          second_head: {
            isTemplate: true,
            templateName: "view",
            width: "120px",
          }
        },
        {
          label: "是否生成",
          prop: "status",
          sortable: true,
          second_head: {
            isTemplate: true,
            templateName: "status",
            templateHeadName: "status_head",
            prop: "status",
            width: "150px",
          }
        },
        {
          label: "学习时间",
          prop: "ctime",
          sortable: true,
          width: "160px",
          second_head: {
            templateHeadName: "ctime_head",
            prop: "ctime",
            width: "160px",
          }
        },
        {
          label: "操作",
          isTemplate: true,
          templateName: "operation",
          width: "120px",
          second_head: {
            isTemplate: true,
            templateName: "operation",
            width: "120px",
          }
        }
      ],
      tableHeadShow: true,
      params: {
        page: 1,
        limit: 10,
        src_ip: "",
        direction: "",
        dest_ip: "",
        dest_port: "",
        protocols: "",
        view: "",
        status: "",
        ctime: ""
      },
      select: {
        // 流量方向
        direction: [
          {
            id: 1,
            name: "出站"
          },
          {
            id: 2,
            name: "入站"
          }
        ],

        // 协议
        protocols: [
          {
            id: 1,
            name: "TCP"
          },
          {
            id: 2,
            name: "UDP"
          },
          {
            id: 3,
            name: "All"
          }
        ],

        // 是否生成
        is_generate: [
          {
            id: 1,
            name: "已生成"
          },
          {
            id: 0,
            name: "未生成"
          }
        ]
      },
      selectData: [],
      icon: {
        stateIcon:this.$icons.stateIcon,
        linkIcon:this.$icons.linkIcon,
        directionIcon:this.$icons.directionIcon
      },
      pageParams: {
        layout: "total, sizes, prev, pager, next, jumper",
        pageAlign: "left",
        pageSizes: [10, 20, 30]
      }
    };
  },
  computed: {},
  props: {
    host_id: Number,
    mark: String,
    layerid: {
      type: String
    }
  },
  mounted:function(){
  },
  methods: {
    reload() {
      this.$refs.hostservice_netfilter_data.reload();
    },
    search() {
      this.params.page = 1
      this.reload()
    },
    // 刷新
    refresh() {
      this.reload();
    },
    selectChange(select) {
      this.selectData = select;
    },
    // 批量删除
    delAll(item) {
      let _this = this;
      let ids = "";
      if (Array.isArray(item)) {
        if (this.$tools.isArrayNull(item)) {
          this.$message({
            type: "error",
            message: "请选择需要删除的选项!"
          });
          return;
        }
        ids = this.$tools.mergeId(item);
      } else {
        ids = item.id;
      }
      this.$isShowTip(
        "hostservice",
        "hostservice_netfilter",
        "确定删除吗?",
        () => {
          this.$ajax({
            url: "host/" + this.host_id + "/netfilter/learn/data/delete",
            type: "post",
            data: {
              learn_id: ids
            }
          })
            .then(function(res) {
              _this.$message({
                type: "success",
                message: "已成功删除!"
              });
              _this.reload();
            })
            .catch(function() {});
        },
        this
      );
    },
    // 批量生成
    generate(item) {
      let _this = this;
      let ids = "";
      if (Array.isArray(item)) {
        if (this.$tools.isArrayNull(item)) {
          this.$message({
            type: "error",
            message: "未选中任何项,请先选择!"
          });
          return;
        }
        ids = this.$tools.mergeId(item);
      } else {
        ids = item.id;
      }
      this.$isShowTip(
        "hostservice",
        "hostservice_netfilter",
        "确定批量生成吗?",
        () => {
          this.$ajax({
            url: "host/" + this.host_id + "/netfilter/learn/data/createRule",
            type: "post",
            data: {
              learn_id: ids
            }
          })
            .then(function(res) {
              _this.$message({
                type: "success",
                message: "成功批量生成!"
              });
              _this.reload();
            })
            .catch(function() {});
        },
        this
      );
    },
    generate_rule(item) {
      var _this = this;
      this.$ajax({
        url: "host/"+this.host_id+"/netfilter/learn/data/createRule",
        type: "post",
        data: {
          learn_id : item.id
        }
      })
        .then(function(res) {
          _this.$message({
            type: "success",
            message: "已成功生成规则!"
          });
          _this.reload();
        })
        .catch(function() {});
    }
  }
};
</script>
<style scoped>
</style>



