<template>
  <div class="hostservice-netfilter-manRule-lookRule" style="width:100%;margin:30px">
    <div>初始化后系统已有规则:</div>
    <g-el-table
      :url="url"
      :type="type"
      :cloumns="cloumns"
      :params="params"
      :pageParams="pageParams"
      @selectChange="selectChange"
      ref="hostservice_netfilter_manRule_lookRule"
    >
      <!-- 规则状态 -->
      <template v-slot:content="props">
        <el-input
          v-model="params.content"
          size="mini"
          :maxLength="20"
          placeholder="请输入关键字"
          @keyup.enter.native="search"
        ></el-input>
      </template>
      
    </g-el-table>
  </div>
</template>
<script>
var padaDate = function(value) {
  return value < 10 ? "0" + value : value;
};
export default {
  name: "hostservice_netfilter_manRule_lookRule",
  data() {
    return {
      url: "/host/" + this.host_id + "/netfilter/rule/origin/list",
      type: "post",
      cloumns: [
        {
          type: "index",
          fixed: "left",
          label: "序号",
          width: "60px",
          indexMethod: function(index) {
            return index + 1;
          }
        },
        {
          label: "规则状态",
          prop: "content",
          width: "178px",
          second_head: {
            prop: "content",
            templateHeadName: "content",
            align: "center"
          }
        }
      ],
      params: {
        page: 1,
        limit: 10,
        content: ""
      },
      pageParams: {
        layout: "total, sizes, prev, pager, next, jumper",
        pageAlign: "left",
        pageSizes: [10, 20, 30]
      },
      batchData: []
    };
  },
  computed: {},
  props: {
    host_id: Number,
    layerid: {
      type: String
    }
  },
  methods: {
    reload() {
      this.$refs.hostservice_netfilter_manRule_lookRule.reload();
    },
    search() {
      this.params.page = 1
      this.reload()
    },
    selectChange(select) {
    }
  }
};
</script>
<style scoped>
</style>



