<template>
  <div
    class="hostservice_netfilter_autoRuleLearning_edit"
    style="width: 100%;padding: 8px;"
  >
    <mu-form
      :model="formData"
      class="mu-demo-form"
      :label-position="labelPosition"
      label-width="90"
      style="padding-left:10%"
      ref="hostservice_netfilter_autoRuleLearning_edit"
    >
      <mu-form-item prop="alias" label="规则名称" :rules="ruleName">
        <mu-text-field
          v-model="formData.alias"
          :max-length="50"
          style="width:80%"
        ></mu-text-field>
      </mu-form-item>

      <mu-form-item prop="direction" label="规则方向">
        <mu-radio
          v-model="formData.direction"
          value="2"
          label="入站"
          @click="inter"
        ></mu-radio>
        <mu-radio
          v-model="formData.direction"
          value="1"
          label="出站"
          @click="out"
        ></mu-radio>
      </mu-form-item>

      <!-- 来源IP -->
      <div id="source_ip_input" style="width: 100%;">
        <mu-form-item prop="src_ip_type" label="来源IP">
          <mu-radio
            v-model="formData.src_ip_type"
            v-for="item of dropDownData.status"
            :key="item.id"
            :value="item.id"
            :label="item.name"
            @click="src_ip_any"
          ></mu-radio>
        </mu-form-item>
        <!-- 单IP -->
        <mu-form-item prop="src_ip" v-show="formData.src_ip_type == 1">
          <mu-text-field
            v-model="single_ip"
            :max-length="50"
            style="width:60%"
            placeholder="输入完成后请单击添加"
          ></mu-text-field>
          <mu-button color="primary" small @click="add_src_ip" class="btn"
            >添加</mu-button
          >
        </mu-form-item>
        <!-- IP区间 -->
        <mu-form-item v-show="formData.src_ip_type == 2" class="more_ip">
          <mu-text-field
            v-model="more_ip_pre"
            :max-length="50"
            style="width:27%"
            placeholder="输入后请点击添加"
          ></mu-text-field>
          &nbsp;&nbsp;&nbsp;
          <p>-</p>
          &nbsp;&nbsp;&nbsp;
          <mu-text-field
            v-model="more_ip_next"
            :max-length="50"
            style="width:27%"
            placeholder="输入后请点击添加"
          ></mu-text-field>
          <mu-button color="primary" small @click="add_src_ip" class="btn"
            >添加</mu-button
          >
        </mu-form-item>
        <!-- 来源IP显示 -->
        <mu-container class="demo-chip-wrapper">
          <mu-chip
            class="demo-chip"
            v-for="(chip, index) in chips"
            :key="chip"
            color="green"
            @delete="remove(index)"
            delete
            >{{ chip }}</mu-chip
          >
        </mu-container>
      </div>

      <!-- 当规则方向为出站时的来源IP -->
      <div class="hide" id="source_ip">
        <mu-form-item prop="src_ip_type" label="来源IP">
          <mu-radio
            v-model="formData.src_ip_type"
            v-for="item of dropDownData.src_ip_out"
            :key="item.id"
            :value="item.id"
            :label="item.name"
            @click="src_ip_any"
          ></mu-radio>
        </mu-form-item>
        <!-- 单IP -->
        <mu-form-item prop="src_ip" v-show="formData.src_ip_type == 1">
          <mu-text-field
            v-model="single_ip"
            :max-length="50"
            style="width:60%"
            placeholder="输入完成后请单击添加"
          ></mu-text-field>
          <mu-button color="primary" small @click="add_src_ip" class="btn"
            >添加</mu-button
          >
        </mu-form-item>
        <!-- IP区间 -->
        <mu-form-item v-show="formData.src_ip_type == 2" class="more_ip">
          <mu-text-field
            v-model="more_ip_pre"
            :max-length="50"
            style="width:27%"
            placeholder="输入后请点击添加"
          ></mu-text-field>
          &nbsp;&nbsp;&nbsp;
          <p>-</p>
          &nbsp;&nbsp;&nbsp;
          <mu-text-field
            v-model="more_ip_next"
            :max-length="50"
            style="width:27%"
            placeholder="输入后请点击添加"
          ></mu-text-field>
          <mu-button color="primary" small @click="add_src_ip" class="btn"
            >添加</mu-button
          >
        </mu-form-item>
        <!-- 来源IP显示 -->
        <mu-container class="demo-chip-wrapper">
          <mu-chip
            class="demo-chip"
            v-for="(chip, index) in chips"
            :key="chip"
            color="green"
            @delete="remove(index)"
            delete
            >{{ chip }}</mu-chip
          >
        </mu-container>
          <!-- <mu-form-item prop="src_ip" v-if="formData.direction == 1" label="来源IP" :rules="ipRules">
            <mu-text-field
              v-model="formData.src_ip"
              :max-length="50"
              style="width:80%"
              placeholder="请输入具体来源IP或any.any任意地址"
            ></mu-text-field>
          </mu-form-item> -->
      </div>
      <!-- 来源端口 -->
      <mu-form-item prop="src_port_type" label="来源端口" class="type_set">
        <mu-radio
          v-model="formData.src_port_type"
          v-for="item of dropDownData.src_port"
          :key="item.id"
          :value="item.id"
          :label="item.name"
          @click="src_port_any"
        ></mu-radio>
      </mu-form-item>
      <!-- 单端口 -->
      <mu-form-item prop="src_port" v-show="formData.src_port_type == 1">
        <mu-text-field
          v-model="src_port_single"
          :max-length="50"
          style="width:60%"
          placeholder="输入完成后请单击添加"
        ></mu-text-field>
        <mu-button color="primary" small @click="src_port_add" class="btn"
          >添加</mu-button
        >
      </mu-form-item>
      <!-- 端口区间 -->
      <mu-form-item prop="src_port" v-show="formData.src_port_type == 2">
        <mu-text-field
          v-model="src_port_pre"
          :max-length="50"
          style="width:27%"
          placeholder="输入后单击添加"
        ></mu-text-field>
        &nbsp;&nbsp;&nbsp;
        <p>:</p>
        &nbsp;&nbsp;&nbsp;
        <mu-text-field
          v-model="src_port_next"
          :max-length="50"
          style="width:27%"
          placeholder="输入后单击添加"
        ></mu-text-field>
        <mu-button color="primary" small @click="src_port_add" class="btn"
          >添加</mu-button
        >
      </mu-form-item>
      <!-- 来源端口显示 -->
      <mu-container class="demo-chip-wrapper">
        <mu-chip
          class="demo-chip"
          v-for="(chip, index) in src_port_chips"
          :key="chip"
          color="green"
          @delete="src_port_remove(index)"
          delete
          >{{ chip }}</mu-chip
        >
      </mu-container>

      <!-- 目标IP -->
      <div id="dest_ip_input" class="hide" style="width: 100%;">
        <mu-form-item prop="dest_ip_type" label="目标IP">
          <mu-radio
            v-model="formData.dest_ip_type"
            v-for="item of dropDownData.dest_ip"
            :key="item.id"
            :value="item.id"
            :label="item.name"
            @click="dest_ip_any"
          ></mu-radio>
        </mu-form-item>
        <!-- 单IP -->
        <mu-form-item prop="dest_ip_single" v-show="formData.dest_ip_type == 1">
          <mu-text-field
            v-model="dest_ip_single"
            :max-length="50"
            style="width:60%"
            placeholder="输入完成后请单击添加"
          ></mu-text-field>
          <mu-button color="primary" small @click="dest_ip_add" class="btn"
            >添加</mu-button
          >
        </mu-form-item>
        <!-- IP区间 -->
        <mu-form-item prop="dest_ip_pre" v-show="formData.dest_ip_type == 2">
          <mu-text-field
            v-model="dest_ip_pre"
            :max-length="50"
            style="width:27%"
            placeholder="输入后单击添加"
          ></mu-text-field>
          &nbsp;&nbsp;&nbsp;
          <p>-</p>
          &nbsp;&nbsp;&nbsp;
          <mu-text-field
            v-model="dest_ip_next"
            :max-length="50"
            style="width:27%"
            placeholder="输入后单击添加"
          ></mu-text-field>
          <mu-button color="primary" small @click="dest_ip_add" class="btn"
            >添加</mu-button
          >
        </mu-form-item>
        <!-- 目标IP显示 -->
        <mu-container class="demo-chip-wrapper">
          <mu-chip
            class="demo-chip"
            v-for="(chip, index) in dest_ip_chips"
            :key="chip"
            color="green"
            @delete="dest_ip_remove(index)"
            delete
            >{{ chip }}</mu-chip
          >
        </mu-container>
      </div>

      <!-- 当规则方向为出站时的目标IP -->
      <div id="dest_ip">
        <mu-form-item prop="dest_ip_type" label="目标IP">
          <mu-radio
              v-model="formData.dest_ip_type"
              v-for="item of dropDownData.dest_ip_enter"
              :key="item.id"
              :value="item.id"
              :label="item.name"
              @click="dest_ip_any"
          ></mu-radio>
        </mu-form-item>

        <!-- 单IP -->
        <mu-form-item prop="dest_ip" v-show="formData.dest_ip_type == 1">
          <mu-text-field
            v-model="dest_ip_single"
            :max-length="50"
            style="width:60%"
            placeholder="输入完成后请单击添加"
          ></mu-text-field>
          <mu-button color="primary" small @click="dest_ip_add" class="btn">添加</mu-button>
        </mu-form-item>

        <!-- 目标IP显示 -->
        <mu-container  class="demo-chip-wrapper">
          <mu-chip class="demo-chip" v-for="chip, index in dest_ip_chips" :key="chip" color="green" @delete="dest_ip_remove(index)" delete>{{chip}}</mu-chip>
        </mu-container>
        <!-- <mu-form-item prop="dest_ip" v-if="formData.direction == 2" label="目标IP" :rules="ipRules">
          <mu-text-field
            v-model="formData.dest_ip"
            :max-length="50"
            style="width:80%"
            placeholder="请输入具体目标IP"
          ></mu-text-field>
        </mu-form-item> -->
      </div>

      <!-- 目标端口 -->
      <mu-form-item prop="dest_port_type" label="目标端口">
        <mu-radio
          v-model="formData.dest_port_type"
          v-for="item of dropDownData.dest_port"
          :key="item.id"
          :value="item.id"
          :label="item.name"
          @click="dest_port_any"
        ></mu-radio>
      </mu-form-item>

      <!-- 单个端口 -->
      <mu-form-item prop="dest_port" v-show="formData.dest_port_type == 1">
        <mu-text-field
          v-model="dest_port_single"
          :max-length="50"
          style="width:60%"
          placeholder="输入完成后请单击添加"
        ></mu-text-field>
        <mu-button color="primary" small @click="dest_port_add" class="btn"
          >添加</mu-button
        >
      </mu-form-item>

      <!-- 多端口 -->
      <mu-form-item prop="dest_port" v-show="formData.dest_port_type == 2">
        <mu-text-field
          v-model="dest_port_pre"
          :max-length="50"
          style="width:27%"
          placeholder="输入后单击添加"
        ></mu-text-field>
        &nbsp;&nbsp;&nbsp;
        <p>:</p>
        &nbsp;&nbsp;&nbsp;
        <mu-text-field
          v-model="dest_port_next"
          :max-length="50"
          style="width:27%"
          placeholder="输入后单击添加"
        ></mu-text-field>
        <mu-button color="primary" small @click="dest_port_add" class="btn"
          >添加</mu-button
        >
      </mu-form-item>

      <!-- 目标端口显示 -->
      <mu-container class="demo-chip-wrapper">
        <mu-chip
          class="demo-chip"
          v-for="(chip, index) in dest_port_chips"
          :key="chip"
          color="green"
          @delete="dest_port_remove(index)"
          delete
          >{{ chip }}</mu-chip
        >
      </mu-container>

      <mu-form-item prop="protocols" label="协议类型">
        <mu-radio
          v-model="formData.protocols"
          value="1"
          @click="selectProtoAll('1')"
          label="TCP"
        ></mu-radio>
        <mu-radio
          v-model="formData.protocols"
          value="2"
          @click="selectProtoAll('2')"
          label="UDP"
        ></mu-radio>
        <mu-radio
          v-model="formData.protocols"
          @click="selectProtoAll('3')"
          value="3"
          label="All"
        ></mu-radio>
      </mu-form-item>

      <mu-form-item prop="process" label="处置方式">
        <mu-radio v-model="formData.process" value="1" label="允许"></mu-radio>
        <mu-radio v-model="formData.process" value="0" label="拒绝"></mu-radio>
      </mu-form-item>

      <mu-form-item style="margin-left:10%">
        <mu-button color="primary" @click="add">确认添加</mu-button>
        <mu-button color="primary" @click="cancel">放弃</mu-button>
      </mu-form-item>
    </mu-form>
  </div>
</template>
<script>
function lookup() {
  var dest_ip = document.getElementById("dest_ip");
  var dest_ip_input = document.getElementById("dest_ip_input");
  var source_ip = document.getElementById("source_ip");
  var source_ip_input = document.getElementById("source_ip_input");
}
export default {
  name: "hostservice_netfilter_autoRuleLearning_edit",
  data() {
    return {
      chips: [],
      src_port_chips: [],
      dest_ip_chips: [],
      dest_port_chips: [],
      single_ip: "", // 来源ip
      more_ip_pre: "",
      more_ip_next: "",
      src_port_single: "", //来源端口
      src_port_pre: "",
      src_port_next: "",
      dest_ip_single: "", //目标ip
      dest_ip_pre: "",
      dest_ip_next: "",
      dest_port_single: "", //目标端口
      dest_port_pre: "",
      dest_port_next: "",
      labelPosition: "right",
      ruleName: [
        { validate: val => !!val, message: "规则名称不能为空" },
        { validate: val => val.length >= 3 &&  val.length <= 20, message: "规则名称长度应在3-20位之间" }
      ],
      ipRules: [
        { validate: val => !!val, message: "IP地址不能为空" },
        {
          validate: val => this.$validates.ipReg.test(val),
          message: "请输入正确的IP地址,例如:1.1.1.1"
        }
      ],
      formData: {
        id: this.item.id,
        alias: "", // 规则名称
        direction: "1", // 规则方向
        src_ip_type: "1", //来源IP类型
        src_ip: "", // 来源IP
        src_port_type: "1", // 来源端口类型
        src_port: "", // 来源端口
        dest_ip_type: "1", //目标IP类型
        dest_ip: "本机", // 目标IP
        dest_port_type: "1", //目标端口类型
        dest_port: "", // 目标端口
        protocols: "1", // 协议类型
        process: "1" // 处置方式
      },
      dropDownData: {
        // 出站时的来源IP
        src_ip_out: [
          {
            id: "1",
            name: "单IP"
          },
          {
            id: "3",
            name: "Any"
          }
        ],
        status: [
          {
            id: "1",
            name: "单IP"
          },
          {
            id: "2",
            name: "IP区间"
          },
          {
            id: "3",
            name: "Any"
          }
        ],
        src_port: [
          {
            id: "1",
            name: "单端口"
          },
          {
            id: "2",
            name: "端口区间"
          },
          {
            id: "3",
            name: "Any"
          }
        ],
        // 入站时的目标IP
        dest_ip_enter: [
          {
            id: "1",
            name: "单IP"
          },
          {
            id: "3",
            name: "Any"
          }
        ],
        dest_ip: [
          {
            id: "1",
            name: "单IP"
          },
          {
            id: "2",
            name: "IP区间"
          },
          {
            id: "3",
            name: "Any"
          }
        ],
        dest_port: [
          {
            id: "1",
            name: "单端口"
          },
          {
            id: "2",
            name: "端口区间"
          },
          {
            id: "3",
            name: "Any"
          }
        ],
        port_status: [
          {
            id: "1",
            name: "单端口"
          },
          {
            id: "2",
            name: "端口区间"
          },
          {
            id: "3",
            name: "Any"
          }
        ]
      }
    };
  },
  props: {
    item: Object,
    host_id: Number,
    layerid: {
      type: String
    }
  },
  mounted:function(){
    let _this = this
    setTimeout(function(){
      // 如果为出站时
      if(_this.item.direction == 1){
          // 来源IP
          if(_this.item.src_ip == "any"){
            _this.formData.src_ip_type = "3"
          }else{
            _this.chips = _this.item.src_ip.split(','); 
          }
          // 目标IP
          if(_this.item.dest_ip == "any"){
              _this.formData.dest_ip_type = "3"
          }else{
              _this.dest_ip_chips = _this.item.dest_ip.split(',');
          }
          _this.formData.dest_ip = _this.item.dest_ip;
          
          lookup();
          dest_ip.className = "hide",
          dest_ip_input.className = "",
          source_ip.className = "",
          source_ip_input.className = "hide";
      }
      // 如果为入站时
      else{
          // 来源IP
          if(_this.item.src_ip == "any"){
              _this.formData.src_ip_type = "3"
          }else{
              _this.chips = _this.item.src_ip.split(',');
          }
          _this.formData.src_ip = _this.item.src_ip;
          // 目标IP
          if(_this.item.dest_ip == "any"){
              _this.formData.dest_ip_type = "3"
          }else{
              _this.dest_ip_chips = _this.item.dest_ip.split(',');
          }
          lookup();
          dest_ip.className = "",
          dest_ip_input.className = "hide",
          source_ip.className = "hide",
          source_ip_input.className = "";
      }
    },1)
    this.formData.alias = this.item.alias;
    this.formData.direction = this.item.direction.toString();
    // 来源端口
    if(_this.item.src_port == "any"){
        _this.formData.src_port_type = "3"
    }else{
        _this.src_port_chips = _this.item.src_port.split(",");
    }
    _this.formData.src_port = _this.item.src_port;
    // 目标端口
    if(_this.item.dest_port == "any"){
        _this.formData.dest_port_type = "3"
    }else{
        _this.dest_port_chips = _this.item.dest_port.split(",");
    }
    _this.formData.dest_port = _this.item.dest_port;
  _this.formData.dest_ip = _this.item.dest_ip;
  _this.formData.src_ip = _this.item.src_ip;
    this.formData.protocols = this.item.protocols.toString();
    this.formData.process = this.item.process.toString();
    this.selectProtoAll(this.formData.protocols)
  },
  methods: {
    selectProtoAll(val) {
      if (val == 3) {
        this.dropDownData.dest_port = [
          {
            id: "3",
            name: "Any"
          }
        ];
        this.dropDownData.src_port = [
          {
            id: "3",
            name: "Any"
          }
        ];
        this.dest_port_chips = [];
        this.src_port_chips = [];
        this.formData.dest_port_type = "3";
        this.formData.src_port_type = "3";
        this.formData.dest_port = "any";
        this.formData.src_port = "any";
      }else {
        this.dropDownData.dest_port = this.dropDownData.port_status
        this.dropDownData.src_port = this.dropDownData.port_status
      }
    },
    // 来源IP添加
    add_src_ip() {
      let isTest = true;
      if (this.formData.src_ip_type == 1) {
        
        isTest = this.$validates.checkIp(this.chips,this.single_ip);
      } else if (this.formData.src_ip_type == 2) {
        isTest = this.$validates.checkIp(this.chips,this.more_ip_pre, this.more_ip_next);
      }
      if (!isTest) {
        return;
      }
      if (this.formData.src_ip_type == 1) {
        if (this.single_ip != "") {
          this.chips.push(this.single_ip);
        }
        this.single_ip = "";
      } else {
        if (this.more_ip_pre != "" && this.more_ip_next != "") {
          this.chips.push(this.more_ip_pre + "-" + this.more_ip_next);
        }
        this.more_ip_pre = "";
        this.more_ip_next = "";
      }
      this.formData.src_ip = this.chips.join(",");
    },
    // 删除
     remove (index) {
      this.chips.splice(index, 1);
      this.formData.src_ip = this.chips.join(',')
    },
    // 如果选择Any
    src_ip_any() {
      if (this.formData.src_ip_type == 3) {
        this.chips = [];
        this.formData.src_ip = "any";
      }
    },
    // 来源端口添加
    src_port_add() {
      let isTest = true;
      if (this.formData.src_port_type == 1) {
        isTest = this.$validates.checkPort(this.src_port_chips,this.src_port_single);
      } else if (this.formData.src_port_type == 2) {
        isTest = this.$validates.checkPort(
          this.src_port_chips,
          this.src_port_pre,
          this.src_port_next
        );
      }
      if (this.formData.src_port_type == 1) {
        if (this.src_port_single != "") {
          this.src_port_chips.push(this.src_port_single);
        }
        this.src_port_single = "";
      } else {
        if (this.src_port_pre != "" && this.src_port_next != "") {
          this.src_port_chips.push(
            this.src_port_pre + ":" + this.src_port_next
          );
        }
        this.src_port_pre = "";
        this.src_port_next = "";
      }
      this.formData.src_port = this.src_port_chips.join(",");
    },
    src_port_remove(index){
          this.src_port_chips.splice(index, 1);
          this.formData.src_port = this.src_port_chips.join(',')
    },
    src_port_any() {
      if (this.formData.src_port_type == 3) {
        this.src_port_chips = [];
        this.formData.src_port = "any";
      }
    },

    // 目标IP添加
    dest_ip_add() {
      let isTest = true;
      if (this.formData.dest_ip_type == 1) {
        isTest = this.$validates.checkIp(this.dest_ip_chips,this.dest_ip_single);
      } else if (this.formData.dest_ip_type == 2) {
        isTest = this.$validates.checkIp(
          this.dest_ip_chips,
          this.dest_ip_pre,
          this.dest_ip_next
        );
      }
      if (!isTest) {
        return;
      }
      if (this.formData.dest_ip_type == 1) {
        if (this.dest_ip_single != "") {
          this.dest_ip_chips.push(this.dest_ip_single);
        }
        this.dest_ip_single = "";
      } else {
        if (this.dest_ip_pre != "" && this.dest_ip_next != "") {
          this.dest_ip_chips.push(this.dest_ip_pre + "-" + this.dest_ip_next);
        }
        this.dest_ip_pre = "";
        this.dest_ip_next = "";
      }
      this.formData.dest_ip = this.dest_ip_chips.join(",");
    },
    dest_ip_remove(index){
          this.dest_ip_chips.splice(index, 1);
          this.formData.dest_ip = this.dest_ip_chips.join(',')
    },
    dest_ip_any() {
      if (this.formData.dest_ip_type == 3) {
        this.dest_ip_chips = [];
        this.formData.dest_ip = "any";
      }
    },

    // 目标端口添加
    dest_port_add() {
      let isTest = true;
      if (this.formData.dest_port_type == 1) {
        isTest = this.$validates.checkPort(this.dest_port_chips,this.dest_port_single);
      } else if (this.formData.dest_port_type == 2) {
        isTest = this.$validates.checkPort(
          this.dest_port_chips,
          this.dest_port_pre,
          this.dest_port_next
        );
      }
      if (!isTest) {
        return;
      }
      if (this.formData.dest_port_type == 1) {
        if (this.dest_port_single != "") {
          this.dest_port_chips.push(this.dest_port_single);
        }
        this.dest_port_single = "";
      } else {
        if (this.dest_port_pre != "" && this.dest_port_next != "") {
          this.dest_port_chips.push(
            this.dest_port_pre + ":" + this.dest_port_next
          );
        }
        this.dest_port_pre = "";
        this.dest_port_next = "";
      }
      this.formData.dest_port = this.dest_port_chips.join(",");
    },
    dest_port_remove(index){
          this.dest_port_chips.splice(index, 1);
          this.formData.dest_port = this.dest_port_chips.join(',')
    },
    dest_port_any() {
      if (this.formData.dest_port_type == 3) {
        this.dest_port_chips = [];
        this.formData.dest_port = "any";
      }
    },

    reload() {
      this.$refs.hostservice_netfilter_selfLearn.reload();
    },
    // 确认添加
    add() {
      let _this = this;
      let data = this.formData;
      let arr = Object.keys(data);
      let isTest = true;
      this.$refs.hostservice_netfilter_autoRuleLearning_edit
        .validate()
        .then(result => {
          if (result) {
            // console.log(data)
            for (let key of arr) {
            if (data[key] == "") {
              this.$message({
                type: "error",
                message: "来源IP、来源端口、目标IP、目标端口不能为空"
              });
              isTest = false;
              break;
            }
          }
          if (!isTest) {
            return;
          }
          let mydata = JSON.parse(JSON.stringify(this.formData))
          mydata.fid = this.item.fid
          mydata.mac_addr = this.item.mac_addr
          mydata.have_mac_addr = this.item.have_mac_addr
          mydata.type = this.item.type
          mydata.learn_id = this.item.learn_id
            this.$isShowTip(
              "hostservice",
              "hostservice_netfilter",
              "确定修改规则吗？",
              () => {
                this.$ajax({
                  url: "host/" + this.host_id + "/netfilter/auto/rule/modify",
                  type: "post",
                  data: mydata
                })
                  .then(function(res) {
                    _this.$message({
                      type: "success",
                      message: "操作成功!"
                    });
                    _this.$layer.close(_this.layerid);
                    _this.$parent.$refs.hostservice_netfilter_selfLearn.reload();
                  })
                  .catch(function() {});
              },
              this
            );
          }
        });
    },
    cancel() {
      this.$isShowTip(
        "hostservice",
        "hostservice_netfilter",
        "确定放弃吗？",
        () => {
          // 关闭弹层
          this.$layer.close(this.layerid);
        },
        this
      );
    },
    // 入站
    inter() {
      lookup();
      dest_ip.className = "",
      dest_ip_input.className = "hide",
      this.formData.dest_ip = this.item.dest_ip;
      source_ip.className = "hide",
      source_ip_input.className = "";
      this.formData.src_ip = "";
      // this.dest_ip_chips=[];
      if(this.formData.src_ip_type == "3"){
        this.chips = [];
        this.formData.src_ip = 'any';
      }else{
        this.chips = this.item.src_ip.split(',');
        this.formData.src_ip = this.item.src_ip;
      }
      if(this.formData.dest_ip_type == "3"){
        this.dest_ip_chips= [];
        this.formData.dest_ip = 'any';
      }else{
        this.dest_ip_chips=this.item.dest_ip.split(',');
        this.formData.dest_ip = this.item.dest_ip;
      }
    },
    // 出站
    out() {
      lookup();
      dest_ip.className = "hide",
      dest_ip_input.className = "",
      this.formData.dest_ip = "",
      source_ip.className = "",
      source_ip_input.className = "hide",
      this.formData.src_ip = this.item.src_ip;
      if(this.formData.src_ip_type == "3"){
        this.chips = [];
        this.formData.src_ip = 'any';
      }else{
        this.chips = this.item.src_ip.split(',');
        this.formData.src_ip = this.item.src_ip;
      }
      if(this.formData.dest_ip_type == "3"){
        this.dest_ip_chips= [];
        this.formData.dest_ip = 'any';
      }else{
        this.dest_ip_chips=this.item.dest_ip.split(',');
        this.formData.dest_ip = this.item.dest_ip;
      }
    }
  }
};
</script>
<style scoped>
.mu-form-item {
  margin-bottom: 1px;
  padding-bottom: 1px;
}
.type {
  font-size: 5px;
  color: silver;
}
</style>



