<template>
  <div
    class="hostservice_netfilter_manualRule"
    style="width: 100%;padding: 8px;"
  >
    <mu-form
      :model="formData"
      class="mu-demo-form"
      :label-position="labelPosition"
      label-width="90"
      style="padding-left:10%"
      ref="hostservice_netfilter_manualRule"
    >
      <mu-form-item prop="alias" label="规则名称" :rules="ruleName">
        <mu-text-field
          v-model="formData.alias"
          :max-length="50"
          style="width:80%"
        ></mu-text-field>
      </mu-form-item>

      <mu-form-item prop="direction" label="规则方向">
        <mu-radio
          v-model="formData.direction"
          value="2"
          label="入站"
          @click="inter_station"
        ></mu-radio>
        <mu-radio
          v-model="formData.direction"
          value="1"
          label="出站"
          @click="out_station"
        ></mu-radio>
      </mu-form-item>
      <!-- 来源IP -->
      <div id="source_ip_input" style="width: 100%;">
        <mu-form-item prop="src_ip_type" label="来源IP">
          <mu-radio
            v-model="formData.src_ip_type"
            v-for="item of dropDownData.status"
            :key="item.id"
            :value="item.id"
            :label="item.name"
            @click="src_ip_any"
          ></mu-radio>
        </mu-form-item>
        <div>
          <!-- 单IP -->
          <mu-form-item v-show="formData.src_ip_type == 1">
            <mu-text-field
              v-model="single_ip"
              :max-length="50"
              style="width:60%"
              placeholder="输入完成后请单击添加"
            ></mu-text-field>
            <mu-button color="primary" small @click="add_src_ip()" class="btn"
              >添加</mu-button
            >
          </mu-form-item>
          <!-- IP区间 -->
          <mu-form-item v-show="formData.src_ip_type == 2" class="more_ip">
            <mu-text-field
              v-model="more_ip_pre"
              :max-length="50"
              style="width:27%"
              placeholder="输入后请点击添加"
            ></mu-text-field>
            &nbsp;&nbsp;&nbsp;
            <p>-</p>
            &nbsp;&nbsp;&nbsp;
            <mu-text-field
              v-model="more_ip_next"
              :max-length="50"
              style="width:27%"
              placeholder="输入后请点击添加"
            ></mu-text-field>
            <mu-button color="primary" small @click="add_src_ip()" class="btn"
              >添加</mu-button
            >
          </mu-form-item>
        </div>
        <!-- 来源IP显示 -->
        <mu-container class="demo-chip-wrapper">
          <mu-chip
            class="demo-chip"
            v-for="(chip, index) in chips"
            :key="chip"
            color="green"
            @delete="remove(index)"
            delete
            >{{ chip }}</mu-chip
          >
        </mu-container>
      </div>
      <!-- 来源IP为本机 -->
      <div class="hide" id="source_ip">
        <mu-form-item label="来源IP">
          <div>本机</div>
        </mu-form-item>
      </div>
      <!-- 来源端口类型 -->
      <mu-form-item prop="src_port_type" label="来源端口">
        <mu-radio
          v-model="formData.src_port_type"
          v-for="item of dropDownData.src_port"
          :key="item.id"
          :value="item.id"
          :label="item.name"
          @click="src_port_any"
        ></mu-radio>
      </mu-form-item>
      <!-- 单端口 -->
      <mu-form-item prop="src_port" v-show="formData.src_port_type == 1">
        <mu-text-field
          v-model="src_port_single"
          :max-length="50"
          style="width:60%"
          placeholder="输入完成后请单击添加"
        ></mu-text-field>
        <mu-button color="primary" small @click="src_port_add()" class="btn"
          >添加</mu-button
        >
      </mu-form-item>
      <!-- 端口区间 -->
      <mu-form-item prop="src_port" v-show="formData.src_port_type == 2">
        <mu-text-field
          v-model="src_port_pre"
          :max-length="50"
          style="width:27%"
          placeholder="输入后请点击添加"
        ></mu-text-field>
        &nbsp;&nbsp;&nbsp;
        <p>:</p>
        &nbsp;&nbsp;&nbsp;
        <mu-text-field
          v-model="src_port_next"
          :max-length="50"
          style="width:27%"
          placeholder="输入后请点击添加"
        ></mu-text-field>
        <mu-button color="primary" small @click="src_port_add()" class="btn"
          >添加</mu-button
        >
      </mu-form-item>
      <!-- 来源端口显示 -->
      <mu-container class="demo-chip-wrapper">
        <mu-chip
          class="demo-chip"
          v-for="(chip, index) in src_port_chips"
          :key="chip"
          color="green"
          @delete="src_port_remove(index)"
          delete
          >{{ chip }}</mu-chip
        >
      </mu-container>

      <!-- 目标IP -->
      <div id="dest_ip_input" class="hide" style="width: 100%;">
        <mu-form-item prop="dest_ip_type" label="目标IP">
          <mu-radio
            v-model="formData.dest_ip_type"
            v-for="item of dropDownData.dest_ip"
            :key="item.id"
            :value="item.id"
            :label="item.name"
            @click="dest_ip_any"
          ></mu-radio>
        </mu-form-item>
        <!-- 单IP -->
        <mu-form-item prop="dest_ip" v-show="formData.dest_ip_type == 1">
          <mu-text-field
            v-model="dest_ip_single"
            :max-length="50"
            style="width:60%"
            placeholder="输入完成后请单击添加"
          ></mu-text-field>
          <mu-button color="primary" small @click="dest_ip_add()" class="btn"
            >添加</mu-button
          >
        </mu-form-item>
        <!-- IP区间 -->
        <mu-form-item prop="dest_ip" v-show="formData.dest_ip_type == 2">
          <mu-text-field
            v-model="dest_ip_pre"
            :max-length="50"
            style="width:27%"
            placeholder="输入后请点击添加"
          ></mu-text-field>
          &nbsp;&nbsp;&nbsp;
          <p>-</p>
          &nbsp;&nbsp;&nbsp;
          <mu-text-field
            v-model="dest_ip_next"
            :max-length="50"
            style="width:27%"
            placeholder="输入后请点击添加"
          ></mu-text-field>
          <mu-button color="primary" small @click="dest_ip_add()" class="btn"
            >添加</mu-button
          >
        </mu-form-item>
        <!-- 目标IP显示 -->
        <mu-container class="demo-chip-wrapper">
          <mu-chip
            class="demo-chip"
            v-for="(chip, index) in dest_ip_chips"
            :key="chip"
            color="green"
            @delete="target_ip_remove(index)"
            delete
            >{{ chip }}</mu-chip
          >
        </mu-container>
      </div>
      <!-- 目标ip为本机 -->
      <div id="dest_ip">
        <mu-form-item label="目标IP">
          <div>本机</div>
        </mu-form-item>
      </div>
      <!-- 目标端口 -->
      <mu-form-item prop="dest_port_type" label="目标端口">
        <mu-radio
          v-model="formData.dest_port_type"
          v-for="item of dropDownData.dest_port"
          :key="item.id"
          :value="item.id"
          :label="item.name"
          @click="dest_port_any"
        ></mu-radio>
      </mu-form-item>
      <!-- 单端口 -->
      <mu-form-item prop="dest_port" v-show="formData.dest_port_type == 1">
        <mu-text-field
          v-model="dest_port_single"
          :max-length="50"
          style="width:60%"
          placeholder="输入完成后请单击添加"
        ></mu-text-field>
        <mu-button color="primary" small @click="dest_port_add()" class="btn"
          >添加</mu-button
        >
      </mu-form-item>
      <!-- 多端口 -->
      <mu-form-item prop="dest_port" v-show="formData.dest_port_type == 2">
        <mu-text-field
          v-model="dest_port_pre"
          :max-length="50"
          style="width:27%"
          placeholder="输入后请点击添加"
        ></mu-text-field>
        &nbsp;&nbsp;&nbsp;
        <p>:</p>
        &nbsp;&nbsp;&nbsp;
        <mu-text-field
          v-model="dest_port_next"
          :max-length="50"
          style="width:27%"
          placeholder="输入后请点击添加"
        ></mu-text-field>
        <mu-button color="primary" small @click="dest_port_add" class="btn"
          >添加</mu-button
        >
      </mu-form-item>

      <!-- 目标端口显示 -->
      <mu-container class="demo-chip-wrapper">
        <mu-chip
          class="demo-chip"
          v-for="(chip, index) in dest_port_chips"
          :key="chip"
          color="green"
          @delete="dest_port_remove(index)"
          delete
          >{{ chip }}</mu-chip
        >
      </mu-container>

      <mu-form-item prop="protocols" label="协议类型">
        <mu-radio
          v-model="formData.protocols"
          value="1"
          @click="selectProtoAll('1')"
          label="TCP"
        ></mu-radio>
        <mu-radio
          v-model="formData.protocols"
          value="2"
          @click="selectProtoAll('2')"
          label="UDP"
        ></mu-radio>
        <mu-radio
          v-model="formData.protocols"
          @click="selectProtoAll('3')"
          value="3"
          label="All"
        ></mu-radio>
      </mu-form-item>

      <mu-form-item prop="process" label="处置方式">
        <mu-radio v-model="formData.process" value="1" label="允许"></mu-radio>
        <mu-radio v-model="formData.process" value="0" label="拒绝"></mu-radio>
      </mu-form-item>

      <mu-form-item style="margin-left:10%">
        <mu-button color="primary" @click="release">下发规则</mu-button>
        <mu-button color="primary" @click="cancel">放弃</mu-button>
      </mu-form-item>
    </mu-form>
  </div>
</template>
<script>
// 查找元素 修改来源IP和目标IP
function lookup() {
  var dest_ip = document.getElementById("dest_ip");
  var dest_ip_input = document.getElementById("dest_ip_input");
  var source_ip = document.getElementById("source_ip");
  var source_ip_input = document.getElementById("source_ip_input");
}

export default {
  name: "hostservice_netfilter_manualRule",
  data() {
    return {
      chips: [],
      src_port_chips: [],
      dest_ip_chips: [],
      dest_port_chips: [],
      single_ip: "", // 来源ip
      more_ip_pre: "",
      more_ip_next: "",
      src_port_single: "", //来源端口
      src_port_pre: "",
      src_port_next: "",
      dest_ip_single: "", //目标ip
      dest_ip_pre: "",
      dest_ip_next: "",
      dest_port_single: "", //目标端口
      dest_port_pre: "",
      dest_port_next: "",
      labelPosition: "right",
      ruleName: [
        { validate: val => !!val, message: "规则名称不能为空" },
        {
          validate: val => val.length >= 3 && val.length <= 20,
          message: "规则名称长度应在3-20位之间"
        }
      ],
      ipRules: [
        { validate: val => !!val, message: "IP地址不能为空" },
        {
          validate: val => this.$validates.ipReg.test(val),
          message: "请输入正确的IP地址,例如:1.1.1.1"
        }
      ],
      formData: {
        host_id: this.host_id,
        alias: "", // 规则名称
        direction: "2", // 规则方向
        src_ip_type: "1", //来源IP类型
        src_ip: "", // 来源IP
        src_port_type: "1", // 来源端口类型
        src_port: "", // 来源端口
        dest_ip_type: "1", //目标IP类型
        dest_ip: "1.1.1.1", // 目标IP
        dest_port_type: "1", //目标端口类型
        dest_port: "", // 目标端口
        protocols: "1", // 协议类型
        process: "1" // 处置方式
      },
      dropDownData: {
        status: [
          {
            id: "1",
            name: "单IP"
          },
          {
            id: "2",
            name: "IP区间"
          },
          {
            id: "3",
            name: "Any"
          }
        ],
        src_port: [
          {
            id: "1",
            name: "单端口"
          },
          {
            id: "2",
            name: "端口区间"
          },
          {
            id: "3",
            name: "Any"
          }
        ],
        dest_ip: [
          {
            id: "1",
            name: "单IP"
          },
          {
            id: "2",
            name: "IP区间"
          },
          {
            id: "3",
            name: "Any"
          }
        ],
        dest_port: [
          {
            id: "1",
            name: "单端口"
          },
          {
            id: "2",
            name: "端口区间"
          },
          {
            id: "3",
            name: "Any"
          }
        ],
        port_status: [
          {
            id: "1",
            name: "单端口"
          },
          {
            id: "2",
            name: "端口区间"
          },
          {
            id: "3",
            name: "Any"
          }
        ]
      }
    };
  },
  props: {
    ipstr: String,
    host_id: String,
    layerid: {
      type: String
    }
  },
  computed: {},
  methods: {
    selectProtoAll(val) {
      if (val == 3) {
        this.dropDownData.dest_port = [
          {
            id: "3",
            name: "Any"
          }
        ];
        this.dropDownData.src_port = [
          {
            id: "3",
            name: "Any"
          }
        ];
        this.dest_port_chips = [];
        this.src_port_chips = [];
        this.formData.dest_port_type = "3";
        this.formData.src_port_type = "3";
        this.formData.dest_port = "any";
        this.formData.src_port = "any";
      } else {
        this.dropDownData.dest_port = this.dropDownData.port_status;
        this.dropDownData.src_port = this.dropDownData.port_status;
      }
    },
    // 来源IP添加
    add_src_ip() {
      let isTest = true;
      if (this.formData.src_ip_type == 1) {
        isTest = this.$validates.checkIp(this.chips, this.single_ip);
      } else if (this.formData.src_ip_type == 2) {
        isTest = this.$validates.checkIp(
          this.chips,
          this.more_ip_pre,
          this.more_ip_next
        );
      }
      if (!isTest) {
        return;
      }
      if (this.formData.src_ip_type == 1) {
        if (this.single_ip != "") {
          this.chips.push(this.single_ip);
        }
        this.single_ip = "";
      } else {
        if (this.more_ip_pre != "" && this.more_ip_next != "") {
          this.chips.push(this.more_ip_pre + "-" + this.more_ip_next);
        }
        this.more_ip_pre = "";
        this.more_ip_next = "";
      }
      this.formData.src_ip = this.chips.join(",");
    },
    // 删除
    remove(index) {
      this.chips.splice(index, 1);
    },
    // 如果选择Any
    src_ip_any() {
      if (this.formData.src_ip_type == 3) {
        this.chips = [];
        this.formData.src_ip = "any";
      }
    },
    // 来源端口添加
    src_port_add() {
      let isTest = true;
      if (this.formData.src_port_type == 1) {
        isTest = this.$validates.checkPort(
          this.src_port_chips,
          this.src_port_single
        );
      } else if (this.formData.src_port_type == 2) {
        isTest = this.$validates.checkPort(
          this.src_port_chips,
          this.src_port_pre,
          this.src_port_next
        );
      }
      if (!isTest) {
        return;
      }
      if (this.formData.src_port_type == 1) {
        // console.log(this.single_ip);
        if (this.src_port_single != "") {
          this.src_port_chips.push(this.src_port_single);
        }
        this.src_port_single = "";
      } else {
        if (this.src_port_pre != "" && this.src_port_next != "") {
          this.src_port_chips.push(
            this.src_port_pre + ":" + this.src_port_next
          );
        }
        this.src_port_pre = "";
        this.src_port_next = "";
      }
      this.formData.src_port = this.src_port_chips.join(",");
    },
    src_port_remove(index) {
      this.src_port_chips.splice(index, 1);
    },
    src_port_any() {
      if (this.formData.src_port_type == 3) {
        this.src_port_chips = [];
        this.formData.src_port = "any";
      }
    },
    // 目标IP添加
    dest_ip_add() {
      let isTest = true;
      if (this.formData.dest_ip_type == 1) {
        isTest = this.$validates.checkIp(
          this.dest_ip_chips,
          this.dest_ip_single
        );
      } else if (this.formData.dest_ip_type == 2) {
        isTest = this.$validates.checkIp(
          this.dest_ip_chips,
          this.dest_ip_pre,
          this.dest_ip_next
        );
      }
      if (!isTest) {
        return;
      }
      if (this.formData.dest_ip_type == 1) {
        // console.log(this.dest_ip_single);
        if (this.dest_ip_single != "") {
          this.dest_ip_chips.push(this.dest_ip_single);
        }
        this.dest_ip_single = "";
      } else {
        if (this.dest_ip_pre != "" && this.dest_ip_next != "") {
          this.dest_ip_chips.push(this.dest_ip_pre + "-" + this.dest_ip_next);
        }
        this.dest_ip_pre = "";
        this.dest_ip_next = "";
      }
      this.formData.dest_ip = this.dest_ip_chips.join(",");
    },
    dest_ip_remove(index) {
      this.dest_ip_chips.splice(index, 1);
    },
    dest_ip_any() {
      if (this.formData.dest_ip_type == 3) {
        this.dest_ip_chips = [];
        this.formData.dest_ip = "any";
      }
    },
    // 目标端口添加
    dest_port_add() {
      let isTest = true;
      if (this.formData.dest_port_type == 1) {
        isTest = this.$validates.checkPort(
          this.dest_port_chips,
          this.dest_port_single
        );
      } else if (this.formData.dest_port_type == 2) {
        isTest = this.$validates.checkPort(
          this.dest_port_chips,
          this.dest_port_pre,
          this.dest_port_next
        );
      }
      if (!isTest) {
        return;
      }

      if (this.formData.dest_port_type == 1) {
        // console.log(this.dest_ip_single);
        if (this.dest_port_single != "") {
          this.dest_port_chips.push(this.dest_port_single);
        }
        this.dest_port_single = "";
      } else {
        if (this.dest_port_pre != "" && this.dest_port_next != "") {
          this.dest_port_chips.push(
            this.dest_port_pre + ":" + this.dest_port_next
          );
        }
        this.dest_port_pre = "";
        this.dest_port_next = "";
      }
      this.formData.dest_port = this.dest_port_chips.join(",");
    },
    dest_port_remove(index) {
      this.dest_port_chips.splice(index, 1);
    },
    dest_port_any() {
      if (this.formData.dest_port_type == 3) {
        this.dest_port_chips = [];
        this.formData.dest_port = "any";
      }
    },
    // 下发规则
    release() {
      let _this = this;
      let data = this.formData;
      let arr = Object.keys(data);
      let isTest = true;
      this.$refs.hostservice_netfilter_manualRule.validate().then(result => {
        if (result) {
          for (let key of arr) {
            if (data[key] == "") {
              this.$message({
                type: "error",
                message: "来源IP、来源端口、目标IP、目标端口不能为空"
              });
              isTest = false;
              break;
            }
          }
          if (!isTest) {
            return;
          }
          this.$isShowTip(
            "hostservice",
            "hostservice_netfilter",
            "确定下发规则吗？",
            () => {
              // 入站
              if (this.formData.direction == 2) {
                this.formData.dest_ip = this.ipstr;
              }
              // 出站
              else if (this.formData.direction == 1) {
                this.formData.src_ip = this.ipstr;
              }
              this.$ajax({
                url: "host/netfilter/handle/rule/add",
                type: "post",
                data: {
                  host_id: this.host_id,
                  alias: this.formData.alias, // 规则名称
                  direction: this.formData.direction, // 规则方向

                  src_ip: this.formData.src_ip, // 来源IP
                  src_port: this.formData.src_port, // 来源端口
                  dest_ip: this.formData.dest_ip, // 目标IP
                  dest_port: this.formData.dest_port, // 目标端口

                  protocols: this.formData.protocols, // 协议类型
                  process: this.formData.process // 处置方式
                }
              })
                .then(function(res) {
                  _this.$message({
                    type: "success",
                    message: "提交成功!"
                  });
                  _this.$layer.close(_this.layerid);
                  // _this.reload();
                  _this.$parent.$refs.hostservice_netfilter_table.reload();
                })
                .catch(function() {});
            },
            this
          );
        }
      });
    },
    // 取消
    cancel() {
      this.$isShowTip(
        "hostservice",
        "hostservice_netfilter",
        "确定放弃吗？",
        () => {
          // 关闭弹层
          this.$layer.close(this.layerid);
        },
        this
      );
    },
    // 入站
    inter_station() {
      lookup();
      dest_ip.className = "";
      dest_ip_input.className = "hide";
      this.formData.dest_ip = "1.1.1.1";
      source_ip.className = "hide";
      source_ip_input.className = "";
      this.formData.src_ip = "";
      // this.src_port_chips=[];
      this.dest_ip_chips = [];
      // this.dest_port_chips=[];
    },
    // 出站
    out_station() {
      lookup();
      dest_ip.className = "hide";
      dest_ip_input.className = "";
      this.formData.dest_ip = "";
      source_ip.className = "";
      source_ip_input.className = "hide";
      this.formData.src_ip = "1.1.1.1";
      this.chips = [];
      // this.src_port_chips = [];
      // this.dest_port_chips = [];
    }
  }
};
</script>
<style scoped>
