<template>
  <div class="log-netfilterlog-detail_log">
    <g-el-table
      :url="url"
      :type="type"
      :cloumns="cloumns"
      :params="params"
      :pageParams="pageParams"
      @selectChange="selectChange"
      :tableHeadShow="tableHeadShow"
      ref="log_netfilterlog_detail_log_table"
    >
      <template v-slot:table_header >
        <el-button type="primary" size="mini" @click="reload">刷新</el-button>
        <el-button type="danger" size="mini" v-if="isIgnore == 0" @click="ignore(selectData)">批量忽略</el-button>
      </template>
      <!-- 操作 -->
      <template slot="operation" slot-scope="props">
        <el-button
          type="danger"
          size="mini"
          v-if="isIgnore == 0"
          @click="ignore(props.data.row)"
        >以后忽略该访问记录</el-button>
        <el-button
          type="danger"
          size="mini"
          v-else-if="isIgnore == 1"
          @click="recover(props.data.row)"
        >恢复此类别的记录</el-button>
      </template>
      <!-- 来源IP -->
      <template v-slot:source_ip_head="props">
        <el-input
          v-model="params.source_ip"
          size="mini"
          placeholder="请输入内容"
          @keyup.enter.native="search"
          :maxLength="20"
        ></el-input>
      </template>
      <!-- 流量方向 -->
      <template slot="direction" slot-scope="props">
        <div v-if="props.data.row.direction == 1">
          <span v-html="icon.directionIcon.outbound"></span>出站
        </div>
        <div v-else-if="props.data.row.direction == 2">
          <span v-html="icon.directionIcon.inbound"></span>入站
        </div>
      </template>
      <template v-slot:direction_head="props">
        <el-select v-model="params.direction" clearable @change="search" size="mini" placeholder="请选择">
          <el-option
            v-for="item in dropDownData.direction"
            :key="item.id"
            :label="item.name"
            :value="item.id"
          ></el-option>
        </el-select>
      </template>
      <!-- 目标IP -->
      <template v-slot:target_ip_head="props">
        <el-input
          v-model="params.target_ip"
          size="mini"
          placeholder="请输入内容"
          @keyup.enter.native="search"
          :maxLength="20"
        ></el-input>
      </template>
      <!-- 目标IP -->
      <template v-slot:target_port_head="props">
        <el-input
          v-model="params.target_port"
          size="mini"
          placeholder="请输入内容"
          @keyup.enter.native="search"
          :maxLength="20"
        ></el-input>
      </template>
      <!-- 协议 -->
      <template
        slot="protocols"
        slot-scope="props"
      >{{props.data.row.protocols == 1 ? "TCP" : "UDP"}}</template>
      <template v-slot:protocols_head="props">
        <el-select v-model="params.protocols" clearable @change="search" size="mini" placeholder="请选择">
          <el-option
            v-for="item in dropDownData.protocols"
            :key="item.id"
            :label="item.name"
            :value="item.id"
          ></el-option>
        </el-select>
      </template>
      <!-- 行为 -->
      <template slot="permission" slot-scope="props">
        <div v-if="props.data.row.permission == 1">
          <span v-html="icon.stateIcon.success"></span>授权
        </div>
        <div v-else-if="props.data.row.permission == 0">
          <span v-html="icon.stateIcon.error"></span>未授权
        </div>
      </template>
      <template v-slot:permission_head="props">
        <el-select v-model="params.permission" clearable @change="search" size="mini" placeholder="请选择">
          <el-option
            v-for="item in dropDownData.permission"
            :key="item.id"
            :label="item.name"
            :value="item.id"
          ></el-option>
        </el-select>
      </template>
      <!-- 首次访问时间 -->
      <template v-slot:first_time_head="props">
        <el-date-picker
          type="date"
          size="mini"
          placeholder="选择日期"
          value-format="yyyy-MM-dd"
          v-model="params.first_time"
          @change="search"
          style="width: 100%;"
        ></el-date-picker>
      </template>
      <!-- 最后访问时间 -->
      <template v-slot:last_time_head="props">
        <el-date-picker
          type="date"
          size="mini"
          placeholder="选择日期"
          value-format="yyyy-MM-dd"
          v-model="params.last_time"
          @change="search"
          style="width: 100%;"
        ></el-date-picker>
      </template>
    </g-el-table>
  </div>
</template>
<script>
import { ignoreLog, recoveryLog } from "req/api/log/netfilterlog";
export default {
  data() {
    return {
      url: `host/${this.host_id}/log/detail/${this.isIgnore}`,
      type: "get",
      cloumns: [
         {
          type: "selection",
          fixed: "left"
        },
        {
          type: "index",
          // fixed: "left",
          label: "序号",
          second_head: {
            type: "index",
            width: "60px",
            label: " ",
            indexMethod: function(index) {
              return index + 1;
            }
          }
        },
        {
          label: "来源IP",
          sortable: true,
          sortMethod: this.$tools.source_ipSort,
          second_head: {
            label: "",
            width: "150px",
            prop: "source_ip"
          }
        },
        {
          label: "流量方向",
          sortable: true,
          prop: "direction",
          width: "150px",
          second_head: {
            isTemplate: true,
            templateName: "direction",
            width: "150px",
            prop: "direction",
            templateHeadName: "direction_head"
          }
        },
        {
          label: "目标IP",
          sortable: true,
          sortMethod: this.$tools.target_ipSort,
          second_head: {
            label: "",
            width: "150px",
            prop: "target_ip"
          }
        },
        {
          label: "目标端口",
          sortable: true,
          prop: "target_port",
          second_head: {
            width: "150px",
            prop: "target_port",
            templateHeadName: "target_port_head"
          }
        },
        {
          label: "协议",
          sortable: true,
          prop: "protocols",
          second_head: {
            isTemplate: true,
            templateName: "protocols",
            width: "150px",
            prop: "protocols",
            templateHeadName: "protocols_head"
          }
        },
        {
          label: "行为",
          sortable: true,
          prop: "permission",
          second_head: {
            isTemplate: true,
            templateName: "permission",
            width: "150px",
            prop: "permission",
            templateHeadName: "permission_head"
          }
        },
        {
          label: "访问次数",
          second_head: {
            label: "",
            prop: "view_all",
            width: "120px"
          }
        },
        {
          label: "首次访问时间",
          sortable: true,
          prop: "first_time",
          second_head: {
            width: "180px",
            prop: "first_time",
            templateHeadName: "first_time_head"
          }
        },
        {
          label: "最后访问时间",
          sortable: true,
          prop: "last_time",
          second_head: {
            width: "180px",
            prop: "last_time",
            templateHeadName: "last_time_head"
          }
        },
        {
          label: "日志管理选项",
          second_head: {
            label: "",
            minWidth: "180px",
            isTemplate: true,
            templateName: "operation"
          }
        }
      ],
      params: {
        page: 1,
        limit: 10,
        source_ip: "",
        direction: "",
        target_ip: "",
        target_port: "",
        protocols: "",
        permission: "",
        first_time: "",
        last_time: ""
      },
      pageParams: {
        layout: "total, sizes, prev, pager, next, jumper",
        pageAlign: "left",
        pageSizes: [10, 20, 30]
      },
      dropDownData: {
        direction: [
          {
            id: 1,
            name: "出站"
          },
          {
            id: 2,
            name: "入站"
          }
        ],
        protocols: [
          {
            id: 1,
            name: "TCP"
          },
          {
            id: 2,
            name: "UDP"
          }
        ],
        permission: [
          {
            id: 0,
            name: "未授权"
          },
          {
            id: 1,
            name: "授权"
          }
        ]
      },
      tableHeadShow: true,
      icon: {
        stateIcon: this.$icons.stateIcon,
        directionIcon: this.$icons.directionIcon
      },
      selectData: []
    };
  },
  created() {
    this.params.source_ip = this.layer_data_details.source_ip;
    this.params.target_ip = this.layer_data_details.target_ip;
  },
  props: {
    layer_data_details: {
      type: Object,
      default: () => {
        return {};
      }
    },
    isIgnore: {
      type: Number,
      default: 0
    },
    host_id: {
      type: String
    }
  },
  methods: {
    reload() {
      this.$refs.log_netfilterlog_detail_log_table.reload();
    },
    search() {
      this.params.page = 1
      this.reload()
    },
    selectChange(select) {
      this.selectData = select;
    },
    ignore(item) {
      let ids = ""
      if(Array.isArray(item)) {
        if(this.$tools.isArrayNull(item)) {
          this.$message({
            type:"error",
            message:"请选择要忽略的选项"
          })
          return
        }
        ids = this.$tools.mergeId(item)
      }else {
        ids = item.id
      }
      this.$isShowTip(
        "hostservice",
        "hostservice_netfilter",
        "确定忽略吗？",
        () => {
          this.__ignoreLog({
            host_id:this.host_id,
            id:ids
          });
        },
        this
      );
    },
    recover(item) {this.$isShowTip(
        "hostservice",
        "hostservice_netfilter",
        "确定恢复吗？",
        () => {
          this.__recoveryLog({
            host_id:this.host_id,
            id:item.id
          });
        },
        this
      );},
    __ignoreLog(param) {
      ignoreLog(param)
        .then(res => {
          this.$message({
            type: "success",
            message: "忽略成功"
          });
          this.reload();
        })
        .catch(err => {});
    },
    __recoveryLog(param) {
      recoveryLog(param)
        .then(res => {
          this.$message({
            type: "success",
            message: "恢复成功"
          });
          this.reload();
        })
        .catch(err => {});
    }
  }
};
</script>
<style >
.log-netfilterlog-detail_log {
  width: 100%;
  padding: 10px;
}
</style>


