<template>
  <div class="netfilter-hit-warning">
    <g-el-table
      :tableHeadShow="tableHeadShow"
      :url="url"
      :type="type"
      :cloumns="cloumns"
      :params="params"
      :pageParams="pageParams"
      @selectChange="selectChange"
      ref="netfilter_hit_warning_table"
    >
      <template v-slot:table_header>
        <el-button type="primary" size="mini" @click="reload()">刷新</el-button>
      </template>
      <!-- 来源IP -->
      <template v-slot:src_ip_head="props">
        <el-input
          v-model="params.src_ip"
          size="mini"
          placeholder="请输入内容"
          @keyup.enter.native="search"
          :maxLength="20"
        ></el-input>
      </template>
      <template v-slot:src_ip="props">
        <span :class="props.data.row.ctime | filterIsNew(firstTime)">
          {{ props.data.row.src_ip }}</span
        >
      </template>

      <!-- 命中规则名 -->
      <template v-slot:rule_name_head="props">
        <el-input
          v-model="params.rule_name"
          size="mini"
          placeholder="请输入内容"
          @keyup.enter.native="search"
          :maxLength="20"
        ></el-input>
      </template>
      <!-- 来源端口 -->
      <template v-slot:src_port_head="props">
        <el-input
          v-model="params.src_port"
          size="mini"
          placeholder="请输入内容"
          @keyup.enter.native="search"
          :maxLength="20"
        ></el-input>
      </template>
      <!-- 流量方向 -->
      <template slot="direction" slot-scope="props">
        <div v-if="props.data.row.direction == 1">
          <span v-html="icon.directionIcon.outbound"></span>出站
        </div>
        <div v-else-if="props.data.row.direction == 2">
          <span v-html="icon.directionIcon.inbound"></span>入站
        </div>
      </template>
      <template v-slot:direction_head="props">
        <el-select
          v-model="params.direction"
          clearable
          @change="search"
          size="mini"
          placeholder="请选择"
        >
          <el-option
            v-for="item in dropDownData.direction"
            :key="item.id"
            :label="item.name"
            :value="item.id"
          ></el-option>
        </el-select>
      </template>
      <!-- 目标IP -->
      <template v-slot:dest_ip_head="props">
        <el-input
          v-model="params.dest_ip"
          size="mini"
          placeholder="请输入内容"
          @keyup.enter.native="search"
          :maxLength="20"
        ></el-input>
      </template>
      <!-- 目标端口 -->
      <template v-slot:dest_port_head="props">
        <el-input
          v-model="params.dest_port"
          size="mini"
          placeholder="请输入内容"
          @keyup.enter.native="search"
          :maxLength="20"
        ></el-input>
      </template>
    </g-el-table>
  </div>
</template>
<script>
import warningNew from "common/mixins/warningNew";
export default {
  data() {
    return {
      url: `host/${this.layer_data.id}/netfilter/rule/virtual/hit/list`,
      type: "post",
      tableHeadShow: true,
      cloumns: [
        {
          type: "index",
          fixed: "left",
          label: "序号",
          second_head: {
            type: "index",
            width: "60px",
            label: " ",
            indexMethod: function(index) {
              return index + 1;
            }
          }
        },
        {
          label: "来源IP",
          sortable: true,
          prop: "src_ip",
          sortMethod: this.$tools.src_ipSort,
          width: "150px",
          second_head: {
            width: "150px",
            prop: "src_ip",
            isTemplate: true,
            templateName: "src_ip",
            templateHeadName: "src_ip_head"
          }
        },
        // {
        //   label: "来源端口",
        //   sortable: true,
        //   prop: "src_port",
        //   width: "150px",
        //   second_head: {
        //     width: "150px",
        //     prop: "src_port",
        //     templateHeadName: "src_port_head"
        //   }
        // },
        {
          label: "流量方向",
          sortable: true,
          prop: "direction",
          width: "150px",
          second_head: {
            isTemplate: true,
            templateName: "direction",
            width: "150px",
            prop: "direction",
            templateHeadName: "direction_head"
          }
        },
        {
          label: "目标IP",
          sortable: true,
          prop: "dest_ip",
          sortMethod: this.$tools.dest_ipSort,
          second_head: {
            width: "150px",
            prop: "dest_ip",
            templateHeadName: "dest_ip_head"
          }
        },
        {
          label: "目标端口",
          sortable: true,
          prop: "dest_port",
          second_head: {
            width: "150px",
            prop: "dest_port",
            templateHeadName: "dest_port_head"
          }
        },
        // {
        //   label: "命中数",
        //   prop: "total",
        //   second_head: {
        //     label: "",
        //     prop: "total",
        //     width: "100px"
        //   }
        // },
        {
          label: "命中规则名",
          prop: "rule_name",
          sortable: true,
          second_head: {
            label: "",
            prop: "rule_name",
            minWidth: "200px",
            templateHeadName: "rule_name_head"
          }
        }
      ],
      params: {
        page: 1,
        limit: 10,
        dest_ip: "",
        src_ip: "",
        dest_port: "",
        src_port: ""
      },
      pageParams: {
        layout: "total, sizes, prev, pager, next, jumper",
        pageAlign: "left",
        pageSizes: [10, 20, 30]
      },
      dropDownData: {
        direction: [
          {
            id: 1,
            name: "出站"
          },
          {
            id: 2,
            name: "入站"
          }
        ]
      },
      icon: {
        stateIcon: this.$icons.stateIcon,
        directionIcon: this.$icons.directionIcon
      },
      selectData: []
    };
  },
  props: {
    layer_data: {
      type: Object,
      default: () => {
        return {};
      }
    },
    isIgnore: {
      type: Number,
      default: 0
    }
  },
  mixins: [warningNew("netfilter_hit_warning_table")],
  methods: {
    search() {
      this.params.page = 1;
      this.reload();
    },
    selectChange(select) {
      this.selectData = select;
    }
  }
};
</script>
<style>
.netfilter-hit-warning {
  width: 100%;
  padding: 10px;
}
</style>



