<template>
  <div class="log-netfilterlog-setOptions">
    <mu-form
      :model="formData"
      class="mu-demo-form"
      :label-position="labelPosition"
      label-width="100"
      ref="log_netfilterlog_setOptions"
    >
      <el-divider content-position="left">
        <span class="batchLinux-divider">日志记录参数设置</span>
      </el-divider>

      <mu-form-item prop="name" label="日志保留时间">
        <div class="logTime">
          <mu-select v-model="formData.year">
            <mu-option
              v-for="count in 4"
              :key="count - 1"
              :label="(count - 1).toString()"
              :value="count - 1"
            ></mu-option>
          </mu-select>
          <span>年</span>
          <mu-select v-model="formData.month">
            <mu-option
              v-for="count in 13"
              :key="count - 1"
              :label="(count - 1).toString()"
              :value="count - 1"
            ></mu-option>
          </mu-select>
          <span>月</span>
          <mu-select v-model="formData.day">
            <mu-option
              v-for="count in 31"
              :key="count - 1"
              :label="(count - 1).toString()"
              :value="count - 1"
            ></mu-option>
          </mu-select>
          <span>天</span>
        </div>
      </mu-form-item>
      <mu-form-item prop="frequency" label="日志收集频率" :rules="frequencyRules">
        <mu-select v-model="formData.frequency">
          <mu-option
            v-for="item of dropDownData.frequency"
            :key="item.value"
            :label="item.name"
            :value="item.value"
          ></mu-option>
        </mu-select>
      </mu-form-item>
      <mu-form-item prop="process" label="超期处理">
        <mu-radio
          v-model="formData.process"
          v-for="item of dropDownData.process"
          :key="item.id"
          :value="item.id"
          :label="item.name"
        ></mu-radio>
      </mu-form-item>
      <mu-form-item>
        <mu-button color="primary" small @click="submitSave">确定</mu-button>
        <mu-button @click="cancel" small>取消</mu-button>
      </mu-form-item>
    </mu-form>
  </div>
</template>
<script>
import { readConfig, editConfig } from "req/api/log/netfilterlog";

export default {
  name: "log_netfilterlog_setOptions",
  data() {
    return {
      labelPosition: "right",
      frequencyRules: [
        { validate: val => !!val, message: "日志收集频率不能为空" }
      ],
      formData: {
        host_id: "",
        expired_time: "",
        year: "0",
        month: "0",
        day: "0",
        frequency: 10,
        process: 1
      },
      dropDownData: {
        frequency: [
          {
            value: 10,
            name: "每10秒"
          },
          {
            value: 20,
            name: "每20秒"
          },
          {
            value: 30,
            name: "每30秒"
          },
          {
            value: 60,
            name: "每1分钟"
          },
          {
            value: 300,
            name: "每5分钟"
          },
          {
            value: 900,
            name: "每15分钟"
          },
          {
            value: 1800,
            name: "每30分钟"
          },
          {
            value: 3600,
            name: "每60分钟"
          },
          {
            value: 7200,
            name: "每2小时"
          },
          {
            value: 14400,
            name: "每4小时"
          },
          {
            value: 28800,
            name: "每8小时"
          },
          {
            value: 86400,
            name: "每24小时"
          }
        ],
        process: [
          {
            id: 1,
            name: "自动删除超期日志"
          },
          {
            id: 2,
            name: "超期后继续记录日志"
          }
        ]
      }
    };
  },
  props: {
    host_id: {
      type: String
    },
    layerid: {
      type: String
    },
    isBatch: {
      type: Boolean,
      default: false
    }
  },
  created() {
    if (!this.isBatch) {
      this.__readConfig({
        host_id: this.host_id
      });
    }
    this.formData.host_id = this.host_id
  },
  methods: {
    submitSave() {
      this.$refs.log_netfilterlog_setOptions.validate().then(result => {
        if (result) {
          this.$isShowTip(
            "hostservice",
            "hostservice_netfilter",
            "确定保存吗？",
            () => {
              let seconds = this.setTimeSeconds(
                this.formData.year,
                this.formData.month,
                this.formData.day
              );
              this.formData.expired_time = seconds;
              this.__editConfig(this.formData);
            },
            this
          );
        }
      });
    },
    cancel() {
      this.$isShowTip(
        "hostservice",
        "hostservice_netfilter",
        "确定取消吗？",
        () => {
          this.$layer.close(this.layerid);
        },
        this
      );
    },
    __editConfig(param) {
      editConfig(param)
        .then(res => {
          this.$message({
            type: "success",
            message: "保存成功"
          });
          this.$layer.close(this.layerid);
        })
        .catch(err => {});
    },
    __readConfig(param) {
      readConfig(param)
        .then(res => {
          this.formData.frequency = res.data.frequency;
          this.formData.process = res.data.process;
          this.formData.expired_time = res.data.expired_time;
          let map = this.setTimeDay(res.data.expired_time);
          this.formData.year = map.year;
          this.formData.month = map.month;
          this.formData.day = map.day;
        })
        .catch(err => {});
    },
    setTimeSeconds(year, month, day) {
      let yearSeconds = parseInt(year) * 365 * 24 * 60 * 60;
      let monthSeconds = parseInt(month) * 30 * 24 * 60 * 60;
      let daySeconds = parseInt(day) * 24 * 60 * 60;
      return yearSeconds + monthSeconds + daySeconds;
    },
    setTimeDay(seconds) {
      let expired_time = parseInt(seconds);
      let dayTime = {};
      dayTime.year = (expired_time / (365 * 24 * 60 * 60)) | 0;
      let remainYear = expired_time % (365 * 24 * 60 * 60);
      dayTime.month = (remainYear / (30 * 24 * 60 * 60)) | 0;
      let remainMonth = remainYear % (30 * 24 * 60 * 60);
      dayTime.day = (remainMonth / (24 * 60 * 60)) | 0;
      return dayTime;
    }
  }
};
</script>
<style scoped lang="less">
.log-netfilterlog-setOptions {
  width: 100%;
  padding: 20px 20px 0;
  .logTime {
    display: flex;
    .mu-input.no-empty-state {
      padding: 0 12px;
    }
  }
}
</style>





