<template>
  <div class="hostservice-netfilter-manRule" style="width: 100%;padding: 8px;">
    <g-el-table
      :tableHeadShow="tableHeadShow"
      :url="url"
      :type="type"
      :cloumns="cloumns"
      :params="params"
      :pageParams="pageParams"
      @selectChange="selectChange"
      ref="hostservice_netfilter_manRule"
    >
      <template v-slot:table_header>
        <el-button type="danger" size="mini" @click="delAll(selectData)">批量删除</el-button>
        <el-button type="primary" size="mini" @click="refresh">刷新</el-button>
        <el-button type="primary" size="mini" @click="rule_on(selectData)">批量启用</el-button>
        <el-button type="primary" size="mini" @click="rule_off(selectData)">批量停用</el-button>
        <el-button type="primary" size="mini" @click="addRule()">添加规则</el-button>
        <el-button type="primary" size="mini" @click="lookRule()">查看原有规则</el-button>
      </template>

      <!-- 规则名称 -->
      <template v-slot:alias="props">
        <el-input
          v-model="params.alias"
          size="mini"
          placeholder="请输入关键字"
          @keyup.enter.native="search"
          :maxLength="20"
        ></el-input>
      </template>

      <!-- 规则状态 -->
      <template slot="status" slot-scope="props">
        <div v-if="props.data.row.status == 1">
          <span v-html="icon.stateIcon.success"></span>已启用
        </div>
        <div v-else-if="props.data.row.status == 0">
          <span v-html="icon.stateIcon.error"></span>已停用
        </div>
      </template>
      <template v-slot:status_head="props">
        <el-select v-model="params.status" clearable @change="search" size="mini" placeholder="请选择">
          <el-option
            v-for="item in select.status"
            :key="item.id"
            :label="item.name"
            :value="item.id"
          ></el-option>
        </el-select>
      </template>

      <!-- 源IP地址 -->
      <template v-slot:src_ip="props">
        <el-input
          v-model="params.src_ip"
          size="mini"
          placeholder="请输入关键字"
          @keyup.enter.native="search"
          :maxLength="20"
        ></el-input>
      </template>

      <!-- 源端口 -->
      <template v-slot:src_port="props">
        <el-input
          v-model="params.src_port"
          size="mini"
          placeholder="请输入关键字"
          @keyup.enter.native="search"
          :maxLength="20"
        ></el-input>
      </template>

      <!-- 方向 -->
      <template slot="direction" slot-scope="props">
        <div v-if="props.data.row.direction == 2">
          <span v-html="icon.directionIcon.inbound"></span>入站
        </div>
        <div v-else-if="props.data.row.direction == 1">
          <span v-html="icon.directionIcon.outbound"></span>出站
        </div>
      </template>
      <template v-slot:direction_head="props">
        <el-select v-model="params.direction" clearable @change="search" size="mini" placeholder="请选择">
          <el-option
            v-for="item in select.direction"
            :key="item.id"
            :label="item.name"
            :value="item.id"
          ></el-option>
        </el-select>
      </template>

      <!-- 目标IP地址 -->
      <template v-slot:dest_ip="props">
        <el-input
          v-model="params.dest_ip"
          size="mini"
          placeholder="请输入关键字"
          @keyup.enter.native="search"
          :maxLength="20"
        ></el-input>
      </template>

      <!-- 目标端口 -->
      <template v-slot:dest_port="props">
        <el-input
          v-model="params.dest_port"
          size="mini"
          placeholder="请输入关键字"
          @keyup.enter.native="search"
          :maxLength="20"
        ></el-input>
      </template>

      <!-- 协议 -->
      <template slot="protocols" slot-scope="props">
        <div v-if="props.data.row.protocols == 1">TCP</div>
        <div v-else-if="props.data.row.protocols == 2">UDP</div>
        <div v-else-if="props.data.row.protocols == 3">All</div>
      </template>
      <template v-slot:protocols_head="props">
        <el-select v-model="params.protocols" clearable @change="search" size="mini" placeholder="请选择">
          <el-option
            v-for="item in select.protocols"
            :key="item.id"
            :label="item.name"
            :value="item.id"
          ></el-option>
        </el-select>
      </template>

      <!-- 协议行为 -->
      <template slot="process" slot-scope="props">
        <div v-if="props.data.row.process == 1">
          <span v-html="icon.stateIcon.success"></span>允许
        </div>
        <div v-else-if="props.data.row.process == 0">
          <span v-html="icon.stateIcon.error"></span>禁止
        </div>
      </template>
      <template v-slot:process_head="props">
        <el-select v-model="params.process" clearable @change="search" size="mini" placeholder="请选择">
          <el-option
            v-for="item in select.process"
            :key="item.id"
            :label="item.name"
            :value="item.id"
          ></el-option>
        </el-select>
      </template>

      <!-- 规则管理 -->
      <template v-slot:operation1="props">
        <el-button type="primary" size="mini" @click="manRule_edit(props.data.row)">修改</el-button>
        <el-button type="danger" size="mini" @click="manRule_del(props.data.row)">删除</el-button>
      </template>
    </g-el-table>
  </div>
</template>
<script>
import addRule from "views/hostservice/netfilter/manRule/addRule/"; //添加规则
import lookRule from "views/hostservice/netfilter/manRule/lookRule/"; //查看原有规则规则
import manRule_edit from "views/hostservice/netfilter/manRule/manRule_edit/"; //修改

export default {
  name: "hostservice_netfilter_manRule",
  data() {
    return {
      url: "/host/" + this.host_id + "/netfilter/handle/rule/list",
      type: "post",
      selectData: [],
      cloumns: [
        {
          type: "selection",
          fixed: "left"
        },
        {
          type: "index",
          // fixed: "left",
          label: "序号",
          width: "60px",
          second_head: {
            type: "index",
            width: "60px",
            label: " ",
            indexMethod: function(index) {
              return index + 1;
            }
          }
        },
        {
          label: "规则名称",
          sortable: true,
          prop: "alias",
          second_head: {
            prop: "alias",
            templateHeadName: "alias",
            width: "150px"
          }
        },
        {
          label: "规则状态",
          sortable: true,
          prop: "status",
          second_head: {
            isTemplate: true,
            templateName: "status",
            templateHeadName: "status_head",
            width: "150px",
            prop: "status"
          }
        },
        {
          label: "源IP地址",
          prop: "src_ip",
          sortMethod: this.$tools.src_ipSort,
          sortable: true,
          second_head: {
            templateHeadName: "src_ip",
            width: "150px",
            prop: "src_ip"
          }
        },
        {
          label: "源端口",
          prop: "src_port",
          sortable: true,
          second_head: {
            templateHeadName: "src_port",
            prop: "src_port",
            width: "150px"
          }
        },
        {
          label: "方向",
          prop: "direction",
          sortable: true,
          second_head: {
            isTemplate: true,
            templateName: "direction",
            templateHeadName: "direction_head",
            prop: "direction",
            width: "150px"
          }
        },
        {
          label: "目标IP地址",
          prop: "dest_ip",
          sortMethod: this.$tools.dest_ipSort,
          sortable: true,
          second_head: {
            templateHeadName: "dest_ip",
            prop: "dest_ip",
            width: "150px"
          }
        },
        {
          label: "目标端口",
          prop: "dest_port",
          sortable: true,
          second_head: {
            templateHeadName: "dest_port",
            prop: "dest_port",
            width: "150px"
          }
        },
        {
          label: "协议",
          prop: "protocols",
          sortable: true,
          second_head: {
            isTemplate: true,
            templateName: "protocols",
            templateHeadName: "protocols_head",
            prop: "protocols",
            width: "150px"
          }
        },
        {
          label: "协议行为",
          prop: "process",
          sortable: true,
          second_head: {
            isTemplate: true,
            templateName: "process",
            templateHeadName: "process_head",
            prop: "process",
            width: "150px"
          }
        },
        {
          label: "规则管理",
          prop: "operation1",
          isTemplate: true,
          templateName: "operation1",
          width: "160px",
          second_head: {
            isTemplate: true,
            templateName: "operation1",
            width: "160px",
          }
        }
      ],
      icon: {
        stateIcon:this.$icons.stateIcon,
        linkIcon:this.$icons.linkIcon,
        directionIcon:this.$icons.directionIcon
      },
      tableHeadShow: true,
      params: {
        page: 1,
        limit: 10,
        alias: "", //规则名称
        status: "", // 规则状态
        src_ip: "", // 来源ip
        src_port: "", // 来源端口
        direction: "", // 规则方向
        dest_ip: "", // 目标ip
        dest_port: "", // 目标端口
        protocols: "", // 协议类型
        have_mac_addr: "", // 系统指纹
        process: "" // 处置方式
      },
      select: {
        // 规则状态
        status: [
          {
            id: 1,
            name: "已启用"
          },
          {
            id: 0,
            name: "已停用"
          }
        ],
        // 自动学习状态
        self_learn: [
          {
            id: 1,
            name: "学习中"
          },
          {
            id: 2,
            name: "结束学习"
          }
        ],
        // 规则方向
        direction: [
          {
            id: 2,
            name: "入站"
          },
          {
            id: 1,
            name: "出站"
          }
        ],
        // 协议类型
        protocols: [
          {
            id: 1,
            name: "TCP"
          },
          {
            id: 2,
            name: "UDP"
          },
          {
            id: 3,
            name: "All"
          }
        ],
        // 处置方式
        process: [
          {
            id: 0,
            name: "禁止"
          },
          {
            id: 1,
            name: "允许"
          },
        ]
      },
      pageParams: {
        layout: "total, sizes, prev, pager, next, jumper",
        pageAlign: "left",
        pageSizes: [10, 20, 30]
      }
    };
  },
  props: {
    item: Object,
    host_id: Number,
    layerid: {
      type: String
    }
  },
  computed: {},
  mounted:function(){
  },
  methods: {
    selectChange(select) {
      this.selectData = select;
    },
    search() {
      this.params.page = 1
      this.reload()
    },
    // 刷新
    refresh() {
      this.reload();
    },
    // 批量删除
    delAll(item) {
      let _this = this;
      let fids =""
      if (Array.isArray(item)) {
        if (this.$tools.isArrayNull(item)) {
          this.$message({
            type: "error",
            message: "请选择需要删除的选项!"
          });
          return;
        }
        fids = this.$tools.mergeParam(item,'fid')
      } else {
        fids = item.fid
      }
      this.$isShowTip(
        "hostservice",
        "hostservice_netfilter",
        "确定删除选中项吗?",
        () => {
          this.$ajax({
            url: "host/" + this.host_id + "/netfilter/handle/rule/delete",
            type: "post",
            data: {
              fid: fids
            }
          })
            .then(function(res) {
              _this.$message({
                type: "success",
                message: "已成功删除!"
              });
              _this.reload();
            })
            .catch(function() {});
        },
        this
      );
    },
    // 批量启用
    rule_on(item) {
      let _this = this;
      let fids = "";
      if (Array.isArray(item)) {
        if (this.$tools.isArrayNull(item)) {
          this.$message({
            type: "error",
            message: "未选中任何项,请先选择!"
          });
          return;
        }
        fids = this.$tools.mergeParam(item,'fid')
      } else {
        fids = item.fid
      }
      this.$isShowTip(
        "hostservice",
        "hostservice_netfilter",
        "确定批量启用吗?",
        () => {
          this.$ajax({
            url: "host/" + this.host_id + "/netfilter/handle/rule/on",
            type: "post",
            data: {
              fid: fids
            }
          })
            .then(function(res) {
              _this.$message({
                type: "success",
                message: "成功批量启用!"
              });
              _this.reload();
            })
            .catch(function() {});
        },
        this
      );
    },
    // 批量停用
    rule_off(item) {
      let _this = this;
      let fids = "";
      if (Array.isArray(item)) {
        if (this.$tools.isArrayNull(item)) {
          this.$message({
            type: "error",
            message: "未选中任何项,请先选择!"
          });
          return;
        }
        fids = this.$tools.mergeParam(item,'fid')
      } else {
        fids = item.fid
      }
      this.$isShowTip(
        "hostservice",
        "hostservice_netfilter",
        "确定批量停用吗?",
        () => {
          this.$ajax({
            url: "host/" + this.host_id + "/netfilter/handle/rule/off",
            type: "post",
            data: {
              fid: fids
            }
          })
            .then(function(res) {
              _this.$message({
                type: "success",
                message: "成功批量停用!"
              });
              _this.reload();
            })
            .catch(function() {});
        },
        this
      );
    },
    // 重载
    reload() {
      this.$refs.hostservice_netfilter_manRule.reload();
    },
    selectChange(select) {
      this.selectData = select;
    },
    // 添加规则
    addRule() {
      this.$layer.iframe({
        content: {
          content: addRule,
          parent: this,
          data: {
            item : this.item,
            host_id: this.host_id
          }
        },
        area: ["660px"],
        title: "添加规则",
        shadeClose: false
      });
    },
    // 查看规则
    lookRule() {
      this.$layer.iframe({
        content: {
          content: lookRule,
          parent: this,
          data: {
            host_id: this.host_id
          }
        },
        area: ["800px", "auto"],
        title: "查看原有规则",
        shadeClose: false
      });
    },
    // 修改
    manRule_edit(item) {
      this.$layer.iframe({
        content: {
          content: manRule_edit,
          parent: this,
          data: {
            item: item,
            host_id :this.host_id
          }
        },
        area: ["650px"],
        title: "修改",
        shadeClose: false
      });
    },
    // 删除
    manRule_del(item) {
      let _this = this;
      this.$isShowTip(
        "hostservice",
        "hostservice_netfilter",
        "确定删除选中项吗?",
        () => {
          this.$ajax({
            url: "host/" + this.host_id + "/netfilter/handle/rule/delete",
            type: "post",
            data: {
              fid: item.fid
            }
          })
            .then(function(res) {
              _this.$message({
                type: "success",
                message: "操作成功!"
              });
              _this.reload();
            })
            .catch(function() {});
        },
        this
      );
    }
  }
};
</script>
<style scoped>
</style>



