<template>
  <div class="netfilter-real-warning">
    <g-el-table
      :url="url"
      :type="type"
      :cloumns="cloumns"
      :params="params"
      :pageParams="pageParams"
      @selectChange="selectChange"
      :tableHeadShow="tableHeadShow"
      ref="netfilter_real_warning_table"
    >
      <template v-slot:table_header >
        <el-button type="primary" size="mini" @click="reload">刷新</el-button>
      </template>
      <!-- 来源IP -->
      <template v-slot:source_ip_head="props">
        <el-input
          v-model="params.source_ip"
          size="mini"
          placeholder="请输入内容"
          @keyup.enter.native="search"
          :maxLength="20"
        ></el-input>
      </template>
      <template v-slot:source_ip="props">
        <span :class="props.data.row.last_time | filterIsNew(firstTime)">
          {{ props.data.row.source_ip }}</span
        >
      </template>
      <!-- 流量方向 -->
      <template slot="direction" slot-scope="props">
        <div v-if="props.data.row.direction == 1">
          <span v-html="icon.directionIcon.outbound"></span>出站
        </div>
        <div v-else-if="props.data.row.direction == 2">
          <span v-html="icon.directionIcon.inbound"></span>入站
        </div>
      </template>
      <template v-slot:direction_head="props">
        <el-select v-model="params.direction" clearable @change="search" size="mini" placeholder="请选择">
          <el-option
            v-for="item in dropDownData.direction"
            :key="item.id"
            :label="item.name"
            :value="item.id"
          ></el-option>
        </el-select>
      </template>
      <!-- 目标IP -->
      <template v-slot:target_ip_head="props">
        <el-input
          v-model="params.target_ip"
          size="mini"
          placeholder="请输入内容"
          @keyup.enter.native="search"
          :maxLength="20"
        ></el-input>
      </template>
      <!-- 目标IP -->
      <template v-slot:target_port_head="props">
        <el-input
          v-model="params.target_port"
          size="mini"
          placeholder="请输入内容"
          @keyup.enter.native="search"
          :maxLength="20"
        ></el-input>
      </template>
      <!-- 协议 -->
      <template
        slot="protocols"
        slot-scope="props"
      >{{props.data.row.protocols == 1 ? "TCP" : "UDP"}}</template>
      <template v-slot:protocols_head="props">
        <el-select v-model="params.protocols" clearable @change="search" size="mini" placeholder="请选择">
          <el-option
            v-for="item in dropDownData.protocols"
            :key="item.id"
            :label="item.name"
            :value="item.id"
          ></el-option>
        </el-select>
      </template>
      <!-- 行为 -->
      <template slot="permission" slot-scope="props">
        <div v-if="props.data.row.permission == 1">
          <span v-html="icon.stateIcon.success"></span>授权
        </div>
        <div v-else-if="props.data.row.permission == 0">
          <span v-html="icon.stateIcon.error"></span>未授权
        </div>
      </template>
      <template v-slot:permission_head="props">
        <el-select v-model="params.permission" clearable @change="search" size="mini" placeholder="请选择">
          <el-option
            v-for="item in dropDownData.permission"
            :key="item.id"
            :label="item.name"
            :value="item.id"
          ></el-option>
        </el-select>
      </template>
    </g-el-table>
  </div>
</template>
<script>
import warningNew from "common/mixins/warningNew";
export default {
  data() {
    return {
      url: `host/${this.layer_data.id}/warring/list`,
      type: "post",
      cloumns: [
         {
          type: "selection",
          fixed: "left"
        },
        {
          type: "index",
          // fixed: "left",
          label: "序号",
          second_head: {
            type: "index",
            width: "60px",
            label: " ",
            indexMethod: function(index) {
              return index + 1;
            }
          }
        },
        {
          label: "来源IP",
          sortable: true,
          sortMethod: this.$tools.source_ipSort,
          second_head: {
            label: "",
            minWidth: "150px",
            isTemplate:true,
            templateName:"source_ip",
            templateHeadName:"source_ip_head",
            prop: "source_ip"
          }
        },
        {
          label: "流量方向",
          sortable: true,
          prop: "direction",
          width: "150px",
          second_head: {
            isTemplate: true,
            templateName: "direction",
            width: "150px",
            prop: "direction",
            templateHeadName: "direction_head"
          }
        },
        {
          label: "目标IP",
          sortable: true,
          sortMethod: this.$tools.target_ipSort,
          second_head: {
            label: "",
            templateHeadName:"target_ip_head",
            minWidth: "150px",
            prop: "target_ip"
          }
        },
        {
          label: "目标端口",
          sortable: true,
          prop: "target_port",
          second_head: {
            width: "150px",
            prop: "target_port",
            templateHeadName: "target_port_head"
          }
        },
        {
          label: "协议",
          sortable: true,
          prop: "protocols",
          second_head: {
            isTemplate: true,
            templateName: "protocols",
            width: "150px",
            prop: "protocols",
            templateHeadName: "protocols_head"
          }
        },
        {
          label: "行为",
          sortable: true,
          prop: "permission",
          second_head: {
            isTemplate: true,
            templateName: "permission",
            width: "150px",
            prop: "permission",
            templateHeadName: "permission_head"
          }
        },
        {
          label: "访问次数",
          second_head: {
            label: "",
            prop: "view_all",
            width: "120px"
          }
        }
      ],
      params: {
        page: 1,
        limit: 10,
        source_ip: "",
        direction: "",
        target_ip: "",
        target_port: "",
        protocols: "",
        permission: ""
      },
      pageParams: {
        layout: "total, sizes, prev, pager, next, jumper",
        pageAlign: "left",
        pageSizes: [10, 20, 30]
      },
      dropDownData: {
        direction: [
          {
            id: 1,
            name: "出站"
          },
          {
            id: 2,
            name: "入站"
          }
        ],
        protocols: [
          {
            id: 1,
            name: "TCP"
          },
          {
            id: 2,
            name: "UDP"
          }
        ],
        permission: [
          {
            id: 0,
            name: "未授权"
          },
          {
            id: 1,
            name: "授权"
          }
        ]
      },
      tableHeadShow: true,
      icon: {
        stateIcon: this.$icons.stateIcon,
        directionIcon: this.$icons.directionIcon
      },
      selectData: []
    };
  },
  props: {
    layer_data:Object,
    host_id: {
      type: String
    }
  },
  mixins: [warningNew("netfilter_real_warning_table")],
  methods: {
    search() {
      this.params.page = 1
      this.reload()
    },
    selectChange(select) {
      this.selectData = select;
    }
  }
};
</script>
<style >
.netfilter-real-warning {
  width: 100%;
  padding: 10px;
}
</style>


