<template>
  <div class="hostservice_netfilter_deploy" style="width: 100%;padding: 8px;">
    <mu-form
      :model="formData"
      class="mu-demo-form"
      :label-position="labelPosition"
      label-width="90"
      ref="hostservice_netfilter_deploy"
    >
      <p>防火墙默认配置</p>
      <mu-form-item prop="select" label="防火墙开关 " label-width="200">
        <mu-flex class="select-control-row">
          <mu-switch v-model="formData.switch" :label="formData.switch ? '开' : '关'" @click="turn"></mu-switch>
        </mu-flex>
      </mu-form-item>
      <div id="configure_show">
        <mu-form-item prop="radio" label="出站默认处理方式" label-width="200">
          <mu-radio v-model="formData.out" value="0" label="阻止"></mu-radio>
          <mu-radio v-model="formData.out" value="1" label="允许"></mu-radio>
        </mu-form-item>
        <mu-form-item prop="radio" label="入站默认处理方式" label-width="200">
          <mu-radio v-model="formData.enter" value="0" label="阻止"></mu-radio>
          <mu-radio v-model="formData.enter" value="1" label="允许"></mu-radio>
        </mu-form-item>
        <!-- <div class="xian"></div>
        <p style="padding:0 30px;font-size:14px" class="remarks">
          注意:当进行了规则下发操作后,系统将不再使用【入站默认处理方式】配置,而是采用
          【规则下发后默认入站处理方式】配置
        </p>
        <mu-form-item prop="radio" label="规则下发后入站默认处理方式" label-width="200">
          <mu-radio v-model="formData.release" value="0" label="阻止" style="font-size:30px"></mu-radio>
          <mu-radio v-model="formData.release" value="1" label="允许"></mu-radio>
        </mu-form-item> -->
      </div>
      <mu-form-item style="margin-left:10%">
        <mu-button color="primary" @click="sure">确定</mu-button>
        <mu-button color="primary" @click="cancel">取消</mu-button>
      </mu-form-item>
    </mu-form>
  </div>
</template>
<script>
var padaDate = function(value) {
  return value < 10 ? "0" + value : value;
};
export default {
  name: "hostservice_netfilter_deploy",
  data() {
    return {
      labelPosition: "right",
      out: "a", // 出站默认处理方式
      enter: "b", // 入站默认处理方式
      // release: "c", //规则下发后入站默认处理方式
      formData: {
        radio: 1,
        switch: false,
        out: "",
        enter: "",
        // release: ""
      }
    };
  },
  props: {
    host_id: Number,
    firewall: String,
    layerid: {
      type: String
    }
  },
  mounted: function() {
    var _this = this;
    var firewall_config = this.firewall;
    var status = firewall_config.substring(0, 1); // 防火墙开关
    var out = firewall_config.substring(1, 2); // 出站默认方式
    var enter = firewall_config.substring(2, 3); // 入站默认方式
    // var release = firewall_config.substring(3); // 规则下发后入站默认处理方式
    setTimeout(function() {
      var configure_show = document.getElementById("configure_show");
      if (status == "1") {
        configure_show.className = "show";
        _this.formData.switch = true;
      } else {
        configure_show.className = "hide";
      }
      _this.formData.out = out;
      _this.formData.enter = enter;
      // _this.formData.release = release;
    }, 1);
  },
  created() {},
  methods: {
    reload() {
      this.$parent.$refs.hostservice_netfilter_table.reload();
    },
    cancel() {
      this.$isShowTip(
        "hostservice",
        "hostservice_netfilter",
        "确定放弃吗？",
        () => {
          // 关闭弹层
          this.$layer.close(this.layerid);
        },
        this
      );
    },
    sure() {
      let _this = this;
      var turn = '';
      if(_this.formData.switch == false){
          turn = 0
      }else{
          turn = 1
      }
      let firewall = turn+ _this.formData.out+_this.formData.enter
      // let firewall = turn+ _this.formData.out+_this.formData.enter+_this.formData.release
      this.$isShowTip(
        "hostservice",
        "hostservice_netfilter",
        "确定配置吗？",
        () => {
          // 关闭弹层
          this.$ajax({
            url: "host/" + this.host_id + "/netfilter/firewall/config/modify",
            type: "post",
            data: {
              firewall:firewall,
            },
          })
            .then(function(res) {
                _this.$message({
                  type: "success",
                  message: "操作成功!"
                });
                _this.$layer.close(_this.layerid);
                _this.reload();
            })
            .catch(function() {});
        },
        this
      );
    },
    turn() {
      var configure_show = document.getElementById("configure_show");
      configure_show.className = "hide";
      if (this.formData.switch == false) {
        configure_show.className = "hide";
      } else {
        configure_show.className = "h";
      }
    }
  }
};
</script>
<style scoped>
.xian {
  height: 1px;
  background-color: #ccc;
  margin: 5px 10px;
}
.mu-form-item {
  padding-left: 10%;
  margin: 0;
}
.mu-radio {
  font-size: 10px;
}
.hide {
  display: none;
}
.show {
  display: block;
}
.remarks{
  color: red;
}
</style>



