<template>
  <div class="log-netfilterlog-export_log_task">
    <g-el-table
      :url="url"
      :type="type"
      :cloumns="cloumns"
      :params="params"
      :pageParams="pageParams"
      ref="log_netfilterlog_export_log_task_table"
    >
      <!-- 操作 -->
      <template slot="operation" slot-scope="props">
        <el-button
          type="primary"
          size="mini"
          :disabled="props.data.row.status != 2 ? true : false"
          @click="downloadLog(props.data.row)"
        >
          下载
        </el-button>
        <el-button type="danger" size="mini" @click="del(props.data.row)"
          >删除</el-button
        >
      </template>
      <!-- 进度条 -->
      <template slot="ratio" slot-scope="props">
        <el-progress :percentage="props.data.row.ratio"></el-progress>
      </template>
      <!-- 状态 -->
      <template slot="status" slot-scope="props">
        <div v-if="props.data.row.status == 0">
          <span v-html="icon.stateIcon.pending"></span>任务已创建
        </div>
        <div v-else-if="props.data.row.status == 1">
          <span v-html="icon.stateIcon.pending"></span>任务进行中
        </div>
        <div v-else-if="props.data.row.status == 2">
          <span v-html="icon.stateIcon.success"></span>任务已完成
        </div>
      </template>
    </g-el-table>
  </div>
</template>
<script>
import { delDownload, downloadInfo } from "req/api/download";

export default {
  data() {
    return {
      url: `download/list/1`,
      type: "get",
      cloumns: [
        {
          type: "index",
          fixed: "left",
          label: "序号",
          width: "60px",
          indexMethod: function(index) {
            return index + 1;
          }
        },
        {
          label: "任务创建时间",
          sortable: true,
          prop: "ctime",
          width: "200px"
        },
        {
          label: "状态",
          sortable: true,
          prop: "status",
          isTemplate: true,
          width: "150px",
          templateName: "status"
        },
        {
          label: "进度条",
          prop: "ratio",
          isTemplate: true,
          templateName: "ratio",
          minWidth: "150px"
        },
        {
          label: "操作",
          minWidth: "150px",
          isTemplate: true,
          templateName: "operation"
        }
      ],
      params: {
        page: 1,
        limit: 10
      },
      pageParams: {
        layout: "total, sizes, prev, pager, next, jumper",
        pageAlign: "left",
        pageSizes: [10, 20, 30]
      },
      icon: {
        stateIcon: this.$icons.stateIcon
      }
    };
  },
  methods: {
    reload() {
      this.$refs.log_netfilterlog_export_log_task_table.reload();
    },
    del(item) {
      this.$isShowTip(
        "hostservice",
        "hostservice_netfilter",
        "确定删除吗？",
        () => {
          this.__delDownload({
            module: 1,
            id: item.id
          });
        },
        this
      );
    },
    downloadLog(item) {
      this.__downloadInfo({
        module: 1,
        id: item.id
      });
    },
    __downloadInfo(param) {
      downloadInfo(param)
        .then(res => {
          let url = `http://${res.data.ip}:${res.data.port}/${res.data.path}`;
          this.$tools.downLoad(url, "netfilter日志.zip");
        })
        .catch(err => {});
    },
    __delDownload(param) {
      delDownload(param)
        .then(res => {
          this.$message({
            type: "success",
            message: "删除成功"
          });
          this.reload();
        })
        .catch(err => {});
    }
  }
};
</script>
<style>
.log-netfilterlog-export_log_task {
  width: 100%;
  margin: 10px;
  border: 1px solid #ebeef5;
}
</style>



