<template>
  <div class="hostservice-netfilter-one" style="width: 100%;padding: 8px;">
    <g-el-table
      :url="url"
      :type="type"
      :cloumns="cloumns"
      :params="params"
      :pageParams="pageParams"
      ref="hostservice_netfilter_one"
    >
      <!-- 来源地址 -->
      <template v-slot:src_ip="props">
        <el-input v-model="params.src_ip" :maxLength="20" size="mini" placeholder="请输入关键字" @keyup.enter.native="search"></el-input>
      </template>

      <!-- 目标地址 -->
      <template v-slot:dest_ip="props">
        <el-input
          v-model="params.dest_ip"
          size="mini"
          placeholder="请输入关键字"
          @keyup.enter.native="search"
          :maxLength="20"
        ></el-input>
      </template>

      <!-- 操作 -->
        <template v-slot:operation="props">
          <el-button type="primary" size="mini" @click="autoDateLearning(props.data.row)" >详情</el-button>
        </template>

    </g-el-table>
  </div>
</template>
<script>
import autoDateLearning from "views/hostservice/netfilter/autoDateLearning/"; //自学习数据
export default {
  data() {
    return {
      url: "host/"+this.host_id+"/netfilter/learn/dataStatistics/list",
      type: "post",
      steduytime:'',
      cloumns: [
        {
          type: "index",
          fixed: "left",
          label: "序号",
          second_head: {
            type: "index",
            width: "60px",
            label: " ",
            indexMethod: function(index) {
              return index + 1;
            }
          }
        },
        {
          label: "来源地址",
          prop: "src_ip",
          sortable: true,
          sortMethod: this.$tools.src_ipSort,
          second_head: {
            prop: "src_ip",
            templateHeadName: "src_ip",
            minWidth: "150px"
          }
        },
        {
          label: "目标地址",
          prop: "dest_ip",
          sortable: true,
          sortMethod: this.$tools.dest_ipSort,
          second_head: {
            templateHeadName: "dest_ip",
            prop: "dest_ip",
            minWidth: "150px"
          }
        },
        {
          label: "总出站数",
          prop: "out",
          second_head: {
            label: "",
            prop: "out",
            width: "120px"
          }
        },
        {
          label: "总入站数",
          prop: "in",
          second_head: {
            label: "",
            prop: "in",
            width: "120px"
          }
        },
        {
          label: "总访问次数",
          prop: "all_view",
          second_head: {
            label: "",
            prop: "all_view",
            width: "120px"
          }
        },
        {
          label: "操作",
          isTemplate: true,
          templateName: "operation",
          width: "100px",
          second_head: {
            isTemplate: true,
            templateName: "operation",
            width: "100px",
          }
        }
      ],
      tableHeadShow: true,
      params: {
        page: 1,
        limit: 10,
        src_ip: "",
        dest_ip: "",
      },
      selectData: [],
      pageParams: {
        layout: "total, sizes, prev, pager, next, jumper",
        pageAlign: "left",
        pageSizes: [10, 20, 30]
      }
    };
  },
  computed: {},
  props: {
    host_id: Number,
    layerid: {
      type: String
    }
  },
  methods: {
    reload() {
      this.$refs.hostservice_netfilter_one.reload()
    },
    search() {
      this.params.page = 1
      this.reload()
    },
    autoDateLearning(item) {
      //  自学习数据
      this.$layer.iframe({
        content: {
          content: autoDateLearning,
          parent: this,
          data: {
            host_id: this.host_id,
            mark: item.mark,
          }
        },
        area: ["1400px",'auto'],
        title: "自学习数据详情",
        shadeClose: false
      });
    },
  }
};
</script>
<style scoped>
</style>



