<template>
  <div class="hostservice-netfilter-exportRule" style="width:100%;margin:30px">
    <g-el-table
      :url="url"
      :type="type"
      :cloumns="cloumns"
      :params="params"
      :pageParams="pageParams"
      @selectChange="selectChange"
      ref="hostservice_netfilter_exportRule"
    >
      <!-- 操作 -->
      <template v-slot:operation="props">
        <el-button type="primary" size="mini" :disabled="props.data.row.status != 2 ? true : false" @click="download(props.data.row)">
          下载
        </el-button>
        <el-button type="danger" size="mini" @click="del(props.data.row)">删除</el-button>
      </template>

      <!-- 任务创建时间 -->
      <template slot="ctime" slot-scope="props">
        <div v-html="props.data.row.ctime"></div>
      </template>

      <!-- 状态 -->
      <template slot="status" slot-scope="props">
        <div v-if="props.data.row.status == 0">
          <span v-html="icon.stateIcon.pending"></span>任务已创建
        </div>
        <div v-else-if="props.data.row.status == 1">
          <span v-html="icon.stateIcon.pending"></span>任务进行中
        </div>
        <div v-else-if="props.data.row.status == 2">
          <span v-html="icon.stateIcon.success"></span>任务已完成
        </div>
      </template>

      <!-- 进度条 -->
      <template v-slot:operation1="props">
        <el-progress :percentage="props.data.row.ratio"></el-progress>
      </template>
    </g-el-table>
  </div>
</template>
<script>
import { downloadInfo } from "req/api/download";
export default {
  name: "hostservice_netfilter_exportRule",
  data() {
    return {
      url: "download/list/2",
      type: "get",
      selectChange:[],
      cloumns: [
        {
          type: "index",
          fixed: "left",
          label: "序号",
          width: "60px",
          indexMethod: function(index) {
            return index + 1;
          }
        },
        {
          label: "任务创建时间",
          sortable: true,
          prop: "ctime",
          width: "200px"
        },
        {
          label: "状态",
          sortable: true,
          prop: "status",
          width: "150px",
          isTemplate: true,
          templateName: "status",
        },

        {
          label: "进度条",
          isTemplate: true,
          templateName: "operation1",
          width: "200px"
        },

        {
          label: "操作",
          isTemplate: true,
          templateName: "operation",
          width: "200px"
        }
      ],
      params: {
        page: 1,
        limit: 10,
        status: "",
        ctime: ""
      },
      pageParams: {
        layout: "total, sizes, prev, pager, next, jumper",
        pageAlign: "left",
        pageSizes: [10, 20, 30]
      },
      icon: {
        stateIcon: this.$icons.stateIcon
      },
      batchData: []
    };
  },
  methods: {
    download(item) {
      this.__downloadInfo({
        module: 2,
        id: item.id
      });
    },
    __downloadInfo(param) {
      downloadInfo(param)
        .then(res => {
          let url = `http://${res.data.ip}:${res.data.port}/${res.data.path}`;
          this.$tools.downLoad(url, "规则.zip");
        })
        .catch(err => {});
    },
    reload() {
      this.$refs.hostservice_netfilter_exportRule.reload();
    },
    // 删除
    del(item) {
      var _this = this;
      this.$isShowTip(
        "hostservice",
        "hostservice_netfilter",
        "确定删除吗?",
        () => {
          this.$ajax({
            url: "download/del/2",
            type: "post",
            data: {
              id : item.id,
            }
          })
            .then(function(res) {
              _this.$message({
                type: "success",
                message: "删除成功!"
              });
              _this.reload();
            })
            .catch(function() {});
        },
        this
      );
    }
  }
};
</script>
<style scoped>
.hostservice-netfilter-exportRule {
  width: 100%;
  margin: 10px;
  border: 1px solid #ebeef5;
}
</style>



