<template>
  <div class="hostservice-netfilter-originalRule" style="width:100%;margin:30px">
    <div>保护客户端正常工作的规则:</div>
    <g-el-table
      :url="url"
      :type="type"
      :cloumns="cloumns"
      :params="params"
      :pageParams="pageParams"
      @selectChange="selectChange"
      ref="hostservice_netfilter_originalRule"
    >
      <!-- 规则 -->
      <template slot="content" slot-scope="props">
        <div v-html="props.data.row.content"></div>
      </template>

      <!-- 操作 -->
      <template v-slot:operation="props">
        <el-button type="danger" size="mini" @click="del(props.data.row)">删除</el-button>
      </template>
    </g-el-table>
  </div>
</template>
<script>
export default {
  name: "hostservice_netfilter_originalRule",
  data() {
    return {
      url: "host/" + this.host_id + "/netfilter/rule/origin/list",
      type: "get",
      cloumns: [
        {
          type: "index",
          fixed: "left",
          label: "序号",
          width: "60px",
          indexMethod: function(index) {
            return index + 1;
          }
        },
        {
          label: "规则",
          sortable: true,
          prop: "content",
          align: "center"
        },

        {
          label: "操作",
          isTemplate: true,
          templateName: "operation",
          align: "center"
        }
      ],

      params: {
        page: 1,
        limit: 10,
        content: ""
      },
      pageParams: {
        layout: "total, sizes, prev, pager, next, jumper",
        pageAlign: "left",
        pageSizes: [10, 20, 30]
      },
      batchData: []
    };
  },
  computed: {},
  props: {
    host_id: Number,
    layerid: {
      type: String
    }
  },
  methods: {
    // 重载
    reload() {
      this.$refs.hostservice_netfilter_originalRule.reload();
    },
    selectChange(select) {
      this.batchData = select;
    },
    del(item){
        let _this = this;
        this.$isShowTip(
        "hostservice",
        "hostservice_netfilter",
        "确定删除选中项吗?",
        () => {
          this.$ajax({ 
            url: "host/" + this.host_id + "/netfilter/rule/origin/delete",
            type: "post",
            data: {
              id: item.id
            }
          })
            .then(function(res) {
              _this.$message({
                type: "success",
                message: "删除成功!"
              });
              _this.reload();
            })
            .catch(function() {});
        },
        this
      );
    }
  }
};
</script>
<style scoped>
</style>



