<template>
  <div class="log-netfilterlog-show_log">
    <g-el-table
      :tableHeadShow="tableHeadShow"
      :url="url"
      :type="type"
      :cloumns="cloumns"
      :params="params"
      :pageParams="pageParams"
      @selectChange="selectChange"
      ref="log_netfilterlog_show_log_table"
    >
      <template v-slot:table_header>
        {{ layer_data.name }}
        <span v-show="isIgnore == 1">被忽略的</span>主机日志
      </template>
      <!-- 操作 -->
      <template slot="operation" slot-scope="props">
        <el-button type="primary" size="mini" @click="detail(props.data.row)"
          >详情</el-button
        >
      </template>
      <!-- 来源IP -->
      <template v-slot:source_ip_head="props">
        <el-input
          v-model="params.source_ip"
          size="mini"
          placeholder="请输入内容"
          @keyup.enter.native="search"
          :maxLength="20"
        ></el-input>
      </template>
      <!-- 目标IP -->
      <template v-slot:target_ip_head="props">
        <el-input
          v-model="params.target_ip"
          size="mini"
          placeholder="请输入内容"
          @keyup.enter.native="search"
          :maxLength="20"
        ></el-input>
      </template>
    </g-el-table>
  </div>
</template>
<script>
import detailLog from "./detail_log";
export default {
  data() {
    return {
      url: `host/${this.layer_data.id}/log/list/${this.isIgnore}`,
      type: "get",
      tableHeadShow: true,
      cloumns: [
        {
          type: "index",
          fixed: "left",
          label: "序号",
          second_head: {
            type: "index",
            width: "60px",
            label: " ",
            indexMethod: function(index) {
              return index + 1;
            }
          }
        },
        {
          label: "来源IP",
          sortable: true,
          prop: "source_ip",
          sortMethod: this.$tools.source_ipSort,
          width: "150px",
          second_head: {
            width: "150px",
            prop: "source_ip",
            templateHeadName: "source_ip_head"
          }
        },
        {
          label: "目标IP",
          sortable: true,
          prop: "target_ip",
          sortMethod: this.$tools.target_ipSort,
          second_head: {
            width: "150px",
            prop: "target_ip",
            templateHeadName: "target_ip_head"
          }
        },
        {
          label: "总出站数",
          prop: "total_out",
          second_head: {
            label: "",
            prop: "total_out",
            width: "120px"
          }
        },
        {
          label: "总入站数",
          prop: "total_input",
          second_head: {
            label: "",
            prop: "total_input",
            width: "120px"
          }
        },
        {
          label: "总条数",
          prop: "total",
          second_head: {
            label: "",
            prop: "total",
            width: "100px"
          }
        },
        {
          label: "操作",
          second_head: {
            label: "",
            minWidth: "100px",
            isTemplate: true,
            templateName: "operation"
          }
        }
      ],
      params: {
        page: 1,
        limit: 10,
        target_ip: "",
        source_ip: ""
      },
      pageParams: {
        layout: "total, sizes, prev, pager, next, jumper",
        pageAlign: "left",
        pageSizes: [10, 20, 30]
      },
      dropDownData: {
        ratio: [
          {
            id: 1,
            name: "TCP"
          },
          {
            id: 2,
            name: "UDP"
          }
        ],
        status: [
          {
            id: 0,
            name: "开启失败"
          },
          {
            id: 1,
            name: "开启成功"
          }
        ]
      },
      icon: {
        stateIcon: this.$icons.stateIcon
      },
      selectData: []
    };
  },
  props: {
    layer_data: {
      type: Object,
      default: () => {
        return {};
      }
    },
    isIgnore: {
      type: Number,
      default: 0
    }
  },
  methods: {
    reload() {
      this.$refs.log_netfilterlog_show_log_table.reload();
    },
    search() {
      this.params.page = 1;
      this.reload();
    },
    selectChange(select) {
      this.selectData = select;
    },
    detail(item) {
      this.$layer.iframe({
        content: {
          content: detailLog,
          parent: this,
          data: {
            layer_data_details: item,
            isIgnore: this.isIgnore,
            host_id: this.layer_data.id.toString()
          }
        },
        area: ["auto", "auto"],
        title: "日志详情",
        shadeClose: false
      });
    }
  }
};
</script>
<style>
.log-netfilterlog-show_log {
  width: 100%;
  padding: 10px;
}
</style>



