import { ajaxPro } from "req/http/http";

export function logList(param) {
  return ajaxPro({
    url: `host/${param.id}/log/list/${param.is_ignore}`,
    type: "post",
    data: param
  })
    .then(res => {
      return Promise.resolve(res);
    })
    .catch(err => {
      return Promise.reject(err);
    });
}

