<template>
  <div class="log-netfilterlog-create_export_task">
    <mu-container>
      <mu-form
        :model="formData"
        class="mu-demo-form"
        :label-position="labelPosition"
        label-width="80"
        ref="log_netfilterlog_create_export_task"
      >
      <el-divider content-position="left">
        <span class="batchLinux-divider">请选择导出日志时间范围</span>
      </el-divider>
        <mu-form-item prop="start_time" label="开始时间" :rules="start_timeRules">
           <mu-text-field
              v-model="formData.start_time"
              :max-length="50"
              @click="openDate('custom_time_picker_1')"
              ref="custom_date_self_1"
            ></mu-text-field>
            <div class="form_date_custom">
              <el-date-picker
                v-model="formData.start_time"
                ref="custom_time_picker_1"
                format="yyyy-MM-dd"
                value-format="yyyy-MM-dd"
                type="date"
                placeholder="选择日期"
                @change="openDate('custom_date_self_1')"
              >
              </el-date-picker>
            </div>
           
            <!-- <mu-date-input v-model="formData.start_time" label-float full-width no-display></mu-date-input> -->
          </mu-form-item>
        <mu-form-item prop="end_time" label="结束时间" :rules="end_timeRules">
            <!-- <mu-date-input v-model="formData.end_time" label-float full-width no-display></mu-date-input> -->
          <mu-text-field
              v-model="formData.end_time"
              :max-length="50"
              @click="openDate('custom_time_picker_2')"
              ref="custom_date_self_2"
            ></mu-text-field>
            <div class="form_date_custom">
              <el-date-picker
                v-model="formData.end_time"
                ref="custom_time_picker_2"
                format="yyyy-MM-dd"
                value-format="yyyy-MM-dd"
                type="date"
                placeholder="选择日期"
                @change="openDate('custom_date_self_2')"
              >
              </el-date-picker>
            </div>
          
          </mu-form-item>
        <mu-form-item>
          <mu-button color="primary" small  @click="submit">确定</mu-button>
          <mu-button small @click="cancel">取消</mu-button>
        </mu-form-item>
      </mu-form>
    </mu-container>
  </div>
</template>
<script>
import {createDownload } from "req/api/download.js";
import openDatePicker from "common/js/datebug"
export default {
  name: "log_netfilterlog_create_export_task",
  data() {
    return {
      labelPosition: "right",
      end_timeRules: [
        { validate: val => !!val, message: "结束时间不能为空" }
      ],
      start_timeRules: [
        { validate: val => !!val, message: "开始时间不能为空" }
      ],
      formData: {
        host_id: "",
        end_time: "",
        start_time: "",
        module:1
      }
    };
  },
  props: {
    host_id: {
      type: String,
      default: ""
    },
    layerid: {
      type: String
    }
  },
  created () {
    this.formData.host_id = this.host_id
  },
  methods: {
     openDate(param) {
      let _this = this
      openDatePicker(param,_this)
    },
    submit() {
      this.$refs.log_netfilterlog_create_export_task.validate().then(result => {
        if (result) {
          this.$isShowTip(
            "hostservice",
            "hostservice_netfilter",
            "确定提交吗？",
            () => {
              this.__createDownload(this.formData)
            },
            this
          );
        }
      });
    },
    cancel() {
      this.$isShowTip(
        "hostservice",
        "hostservice_netfilter",
        "确定取消吗？",
        () => {
          this.$layer.close(this.layerid);
        },
        this
      );
    },
    __createDownload(param) {
      createDownload(param)
        .then(res => {
          this.$message({
            type: "success",
            message: "创建成功"
          });
          this.$layer.close(this.layerid)
        })
        .catch(err => {});
    }
  }
};
</script>
<style scoped lang="less">
.log-netfilterlog-create_export_task {
  width: 100%;
  padding: 10px 20px 0 0;
}
</style>





