<template>
  <div class="hostservice-netfilter-defaultRule" style="width:100%;margin:30px">
    <div>保护客户端正常工作的规则:</div>
    <g-el-table
      :url="url"
      :type="type"
      :cloumns="cloumns"
      :params="params"
      :pageParams="pageParams"
      @selectChange="selectChange"
      ref="hostservice_netfilter_defaultRule"
    >
      <!-- 规则 -->
      <template slot="content" slot-scope="props">
        <div v-html="props.data.row.content"></div>
      </template>

      <!-- 操作 -->
      <template v-slot:operation="props">
        <!-- <el-button type="danger" size="mini" @click="del(props.data.row)">删除</el-button> -->
        <div v-if="props.data.row.status == 1">
          <el-button
            type="primary"
            size="mini"
            @click="forbid_rule(props.data.row)"
            >规则已启用</el-button
          >
        </div>
        <div v-if="props.data.row.status == 0">
          <el-button
            type="danger"
            size="mini"
            @click="enable_rule(props.data.row)"
            >规则未启用</el-button
          >
        </div>
      </template>
    </g-el-table>
  </div>
</template>
<script>
export default {
  name: "hostservice_netfilter_defaultRule",
  data() {
    return {
      url: "host/" + this.host_id + "/netfilter/rule/safe/list",
      type: "get",
      cloumns: [
        {
          type: "index",
          fixed: "left",
          label: "序号",
          width: "60px",
          indexMethod: function(index) {
            return index + 1;
          }
        },
        {
          label: "规则",
          sortable: true,
          prop: "content",
          align: "center"
        },

        {
          label: "操作",
          isTemplate: true,
          templateName: "operation",
          align: "center"
        }
      ],

      params: {
        page: 1,
        limit: 10,
        content: ""
      },
      pageParams: {
        layout: "total, sizes, prev, pager, next, jumper",
        pageAlign: "left",
        pageSizes: [10, 20, 30]
      },
      batchData: []
    };
  },
  computed: {},
  props: {
    host_id: Number,
    layerid: {
      type: String
    }
  },
  methods: {
    // 重载
    reload() {
      this.$refs.hostservice_netfilter_defaultRule.reload();
    },
    selectChange(select) {
      this.batchData = select;
    },
    // 禁用规则
    forbid_rule(item) {
      let _this = this;
      const h = this.$createElement;
      this.$msgbox({
        title: "警告",
        message: h("p", null, [
          h("span", { style: "color: #F56C6C;font-size:14px;font-weight: bold;" }, "危险操作,是否继续? ")
        ]),
        showCancelButton: true,
        confirmButtonText: "确定",
        cancelButtonText: "取消",
        type: "warning"
      })
        .then(() => {
          this.$isShowTip(
            "hostservice",
            "hostservice_netfilter",
            "确定禁用规则吗?",
            () => {
              this.$ajax({
                url: "host/" + this.host_id + "/netfilter/rule/safe/off",
                type: "post",
                data: {
                  rule_id: item.id
                }
              })
                .then(function(res) {
                  _this.$message({
                    type: "success",
                    message: "操作成功!"
                  });
                  _this.reload();
                })
                .catch(function() {});
            },
            this
          );
        })
        .catch(() => {});
    },
    // 启用规则
    enable_rule(item) {
      let _this = this;
      const h = this.$createElement;
      this.$msgbox({
        title: "警告",
        message: h("p", null, [
          h("span", { style: "color: #F56C6C;font-size:14px;font-weight: bold;" }, "危险操作,是否继续? ")
        ]),
        showCancelButton: true,
        confirmButtonText: "确定",
        cancelButtonText: "取消",
        type: "warning"
      })
        .then(() => {
          this.$isShowTip(
            "hostservice",
            "hostservice_netfilter",
            "确定启用规则吗?",
            () => {
              this.$ajax({
                url: "host/" + this.host_id + "/netfilter/rule/safe/on",
                type: "post",
                data: {
                  rule_id: item.id
                }
              })
                .then(function(res) {
                  _this.$message({
                    type: "success",
                    message: "操作成功!"
                  });
                  _this.reload();
                })
                .catch(function() {});
            },
            this
          );
        })
        .catch(() => {});
    }
  }
};
</script>
<style scoped></style>



