<template>
  <div class="hostservice-netfilter">
    <div class="tagIcon-notice">
      <span>系统功能图标表示:</span>
      <span v-for="item in icon.tagIcon" :key="item.id">
        <svg class="icon_1x5" aria-hidden="true" v-html="item.icon" />
        {{ item.name }}
      </span>
    </div>
    <g-el-table
      :tableHeadShow="tableHeadShow"
      :url="url"
      :type="type"
      :cloumns="cloumns"
      :params="params"
      :pageParams="pageParams"
      @selectChange="selectChange"
      ref="hostservice_netfilter_table"
    >
      <template v-slot:table_header>
        <el-button type="primary" size="mini" @click="reload()">刷新</el-button>
        <el-button type="danger" size="mini" @click="removeModule(selectData)"
          >移除主机</el-button
        >
        <el-button type="primary" size="mini" @click="addModule()"
          >添加主机</el-button
        >
        <el-button type="primary" size="mini" @click="blInit(selectData)"
          >批量初始化</el-button
        >
        <el-button type="primary" size="mini" @click="bl(selectData)"
          >批量学习</el-button
        >
        <el-button type="primary" size="mini" @click="manualRule(selectData)"
          >批量下发人工规则</el-button
        >

        <el-button type="primary" size="mini" @click="export_rule(selectData)"
          >批量创建规则导出任务</el-button
        >
        <el-button type="primary" size="mini" @click="exRule(selectData)"
          >导出规则下载</el-button
        >
        <el-button type="primary" size="mini" @click="setLogOptions(selectData)"
          >批量设置日志选项</el-button
        >
        <el-button
          type="primary"
          size="mini"
          @click="createExportTask(selectData)"
          >批量创建日志导出任务</el-button
        >
        <el-button type="primary" size="mini" @click="exportTaskList"
          >导出日志下载</el-button
        >
        <!-- <el-button type="primary" size="mini" @click="statistical(selectData)"
          >端口访问图表</el-button
        > -->
        
      </template>
      <!-- 资产名称 -->
      <template v-slot:name_head="props">
        <el-input
          v-model="params.name"
          size="mini"
          placeholder="请输入关键字"
          @keyup.enter.native="reload"
          :maxLength="20"
        ></el-input>
      </template>
      <!-- IP地址 -->
      <template v-slot:ip="props">
        <el-popover trigger="hover" placement="top">
          <p>主机名: {{ props.data.row.name }}</p>
          <p>业务系统名称: {{ props.data.row.software.name }}</p>
          <div slot="reference" class="name-wrapper">
            {{ props.data.row.ip }}
          </div>
        </el-popover>
      </template>
      <template v-slot:ip_head="props">
        <el-input
          v-model="params.ip"
          :maxLength="20"
          size="mini"
          placeholder="请输入关键字"
          @keyup.enter.native="reload"
        ></el-input>
      </template>
      <!-- 功能类别 -->
      <template slot="tag" slot-scope="props">
        <span v-for="item of props.data.row.tag" :key="item.id">
          <svg
            class="icon_1x5"
            aria-hidden="true"
            v-html="icon.tagIcon[item.icon].icon"
          />
          <i style="font-size:0;">{{ item.name }}</i>
        </span>
      </template>
      <template v-slot:tag_head="props">
        <el-select
          v-model="params.tag"
          @change="search"
          clearable
          size="mini"
          placeholder="请选择"
        >
          <el-option
            v-for="item in icon.tagIcon"
            :key="item.id"
            :label="item.name"
            :value="item.id"
          ></el-option>
        </el-select>
      </template>
      <!-- 状态 -->
      <template slot="online" slot-scope="props">
        <div v-if="props.data.row.online == 1">
          <span v-html="icon.linkIcon.link"></span>在线
          <span v-show="props.data.row.version"
            >[v{{ props.data.row.version }}]</span
          >
        </div>
        <div v-else-if="props.data.row.online == 0">
          <span v-html="icon.linkIcon.unlink"></span>离线<span
            v-show="props.data.row.version"
            >[v{{ props.data.row.version }}]</span
          >
        </div>
      </template>
      <template v-slot:online_sel="props">
        <el-select
          v-model="params.online"
          clearable
          @change="search"
          size="mini"
          placeholder="请选择"
        >
          <el-option
            v-for="item in select.online"
            :key="item.id"
            :label="item.name"
            :value="item.id"
          ></el-option>
        </el-select>
      </template>
      <!-- 自动学习状态 -->
      <template slot="learn_status" slot-scope="props">
        <div v-if="props.data.row.netfilter.learn_status == 1">
          <span v-html="icon.stateIcon.success"></span>学习中
        </div>
        <div v-else-if="props.data.row.netfilter.learn_status == 0">
          <span v-html="icon.stateIcon.error"></span>结束学习
        </div>
      </template>
      <template v-slot:learn_status_sel="props">
        <el-select
          v-model="params.learn_status"
          clearable
          @change="reload"
          size="mini"
          placeholder="请选择"
        >
          <el-option
            v-for="item in select.self_learn"
            :key="item.id"
            :label="item.name"
            :value="item.id"
          ></el-option>
        </el-select>
      </template>
      <!-- 操作系统版本 -->
      <template slot="os" slot-scope="props">
        <svg
          class="icon_1x5"
          aria-hidden="true"
          v-html="icon.osIcon[props.data.row.os].icon"
        />
        <span>{{ icon.osIcon[props.data.row.os].name }}</span>
      </template>
      <template v-slot:os_head="props">
        <el-select
          v-model="params.os"
          clearable
          @change="search"
          size="mini"
          placeholder="请选择"
        >
          <el-option
            v-for="item in icon.osIcon"
            :key="item.id"
            :label="item.name"
            :value="item.id"
          ></el-option>
        </el-select>
      </template>
      <!-- expand -->
      <template slot="expand" slot-scope="props">
        <el-form label-position="left" inline class="ahsmp-table-expand">
          <!-- <el-form-item label="功能类别:">
            <span v-for="item of props.data.row.tag" :key="item.id">
              <svg
                class="icon_1x5"
                aria-hidden="true"
                v-html="icon.tagIcon[item.icon].icon"
              />
              <span>{{ icon.tagIcon[item.icon].name }}</span>
            </span>
          </el-form-item> -->
          <!-- 规则管理 -->
          <el-form-item label="规则管理:">
            <el-button
              v-if="props.data.row.netfilter.init == 0"
              type="primary"
              size="mini"
              class="netfilter_btn_width"
              @click="initialization(props.data.row)"
              :disabled="props.data.row.online == 0"
              >初始化</el-button
            >
            <el-button
              v-if="props.data.row.netfilter.init == 1"
              type="primary"
              size="mini"
              class="netfilter_btn_width"
              disabled
              >已初始化</el-button
            >
            <el-button
              type="primary"
              size="mini"
              class="netfilter_btn_width"
              @click="autoRuleLearning(props.data.row)"
              :disabled="
                props.data.row.online == 0 || props.data.row.netfilter.init == 0
              "
              >自学习规则</el-button
            >

            <el-button
              type="primary"
              size="mini"
              class="netfilter_btn_width"
              @click="manRule(props.data.row)"
              :disabled="
                props.data.row.online == 0 || props.data.row.netfilter.init == 0
              "
              >人工规则</el-button
            >
            <el-button
              type="primary"
              size="mini"
              class="netfilter_btn_width"
              @click="fictitious_Rule(props.data.row)"
              :disabled="
                props.data.row.online == 0 || props.data.row.netfilter.init == 0
              "
              >虚拟规则</el-button
            >
            <el-button
              type="primary"
              size="mini"
              class="netfilter_btn_width"
              @click="defaultRule(props.data.row)"
              :disabled="
                props.data.row.online == 0 || props.data.row.netfilter.init == 0
              "
              >默认规则</el-button
            >
            <el-button
              type="primary"
              size="mini"
              class="netfilter_btn_width"
              @click="originalRule(props.data.row)"
              :disabled="
                props.data.row.online == 0 || props.data.row.netfilter.init == 0
              "
              >原始规则</el-button
            >
          </el-form-item>
          
          <!-- 自学习管理 -->
          <el-form-item label="学习管理:">
            <!-- 初始化 -->
            <div v-if="props.data.row.netfilter.init == 0">
              <el-button
                type="primary"
                size="mini"
                class="netfilter_btn_width"
                disabled
                >开始学习</el-button
              >
              <el-button
                type="primary"
                size="mini"
                @click="LearningConfiguration(props.data.row)"
                disabled
                class="netfilter_btn_width"
                >自学习配置</el-button
              >
              <el-button
                type="primary"
                size="mini"
                @click="autodateLearning_one(props.data.row)"
                disabled
                class="netfilter_btn_width"
                >自学习数据</el-button
              >
            </div>
            <!-- 已初始化 -->
            <div v-if="props.data.row.netfilter.init == 1">
              <el-button
                type="primary"
                size="mini"
                @click="study_turn(props.data.row)"
                v-if="props.data.row.netfilter.learn_status == 0"
                class="is_study netfilter_btn_width"
                :disabled="props.data.row.online == 0"
                >开始学习</el-button
              >
              <el-button
                type="danger"
                size="mini"
                @click="study_turn(props.data.row)"
                v-if="props.data.row.netfilter.learn_status == 1"
                class="is_study netfilter_btn_width"
                :disabled="props.data.row.online == 0"
                >停止学习</el-button
              >
              <el-button
                type="primary"
                size="mini"
                @click="LearningConfiguration(props.data.row)"
                :disabled="props.data.row.online == 0"
                class="netfilter_btn_width"
                >自学习配置</el-button
              >
              <el-button
                type="primary"
                size="mini"
                class="netfilter_btn_width"
                @click="autodateLearning_one(props.data.row)"
                :disabled="props.data.row.online == 0"
                >自学习数据</el-button
              >
            </div>
          </el-form-item>
          <!-- 主机管理 -->
          <el-form-item label="主机管理:">
            <el-button
              type="primary"
              size="mini"
              class="netfilter_btn_width"
              @click="deploy(props.data.row)"
              v-if="props.data.row.netfilter.init == 0"
              disabled
              >配置</el-button
            >
            <el-button
              type="primary"
              size="mini"
              class="netfilter_btn_width"
              @click="deploy(props.data.row)"
              v-if="props.data.row.netfilter.init == 1"
              :disabled="props.data.row.online == 0"
              >配置</el-button
            >
          </el-form-item>
          <!-- 日志管理 -->
          <el-form-item label="日志管理:">
            <el-button
              type="primary"
              size="mini"
              class="netfilter_btn_width"
              @click="openLog(props.data.row)"
              v-if="props.data.row.netfilter.log_status == 0"
              :disabled="props.data.row.online == 0"
              >日志开启</el-button
            >
            <el-button
              type="danger"
              size="mini"
              class="netfilter_btn_width"
              @click="closeLog(props.data.row)"
              :disabled="props.data.row.online == 0"
              v-else
              >日志关闭</el-button
            >
            <el-button
              type="primary"
              size="mini"
              class="netfilter_btn_width"
              @click="showLog(props.data.row)"
              >查看日志</el-button
            >
            <el-button
              type="primary"
              size="mini"
              class="netfilter_btn_width"
              @click="showIngoreLog(props.data.row)"
              >查看被忽略日志</el-button
            >
            <el-button
              type="primary"
              size="mini"
              class="netfilter_btn_width"
              @click="setLogOptions(props.data.row)"
              >日志选项</el-button
            >
            <el-button
              type="primary"
              size="mini"
              class="netfilter_btn_width"
              @click="createExportTask(props.data.row)"
              >创建导出日志任务</el-button
            >
          </el-form-item>
          <!-- 实时告警 -->
          <el-form-item label="实时告警:">
            <el-button
              class="netfilter_btn_width"
              type="primary"
              size="mini"
              @click="hitWaring(props.data.row)"
              :disabled="
                props.data.row.online == 0 || props.data.row.netfilter.init == 0
              "
              >虚拟规则</el-button
            >
            <el-button
              class="netfilter_btn_width"
              type="primary"
              size="mini"
              @click="realWarning(props.data.row)"
              :disabled="
                props.data.row.online == 0 || props.data.row.netfilter.init == 0
              "
              >真实规则</el-button
            >
          </el-form-item>
          
        </el-form>
      </template>
    </g-el-table>
  </div>
</template>

<script>
import bl from "views/hostservice/netfilter/batchLearning/"; //批量学习
import exRule from "views/hostservice/netfilter/exportRule/"; //下载导出规则
import manualRule from "views/hostservice/netfilter/manualRule/"; //批量下发手工规则
import LearningConfiguration from "views/hostservice/netfilter/LearningConfiguration/"; //自学习配置
import autodateLearning_one from "views/hostservice/netfilter/autoDateLearning_one/"; //自学习数据
import autoRuleLearning from "views/hostservice/netfilter/autoRuleLearning/"; //自学习规则
import defaultRule from "views/hostservice/netfilter/defaultRule/"; //默认规则
import originalRule from "views/hostservice/netfilter/originalRule/"; //原始规则
import deploy from "views/hostservice/netfilter/deploy/"; //配置
import manRule from "views/hostservice/netfilter/manRule/"; //人工规则
import fictitious_Rule from "views/hostservice/netfilter/fictitious_Rule/"; //虚拟规则
import lineBrowse from "views/home/line_browse";
import { logOpen, logClose } from "req/api/log/netfilterlog";
import moduleRefresh from "common/mixins/modulRefresh"
import setLogOptions from "./netfilterlog/set_logging_options";
import createExportTask from "./netfilterlog/create_export_task";
import exportLogTaskList from "./netfilterlog/export_log_task_list";
import showLogList from "./netfilterlog/show_log_list";
import addModule from "views/hostservice/addModule";
import { delModule } from "req/api/hostservice/addModule";
import hitAlarm from "views/hostservice/netfilter/hit_warning";
import realAlarm from "views/hostservice/netfilter/real_warning";

export default {
  data() {
    return {
      url: "/host/list/netfilter",
      type: "post",
      cloumns: [
        {
          type: "selection",
          fixed: "left"
        },
        {
          type: "index",
          // fixed: "left",
          label: "序号",
          second_head: {
            type: "index",
            width: "60px",
            label: " ",
            indexMethod: function(index) {
              return index + 1;
            }
          }
        },
        {
          label: "资产名称",
          sortable: true,
          prop: "name",
          second_head: {
            prop: "name",
            templateHeadName: "name_head",
            width: "200px"
          }
        },
        {
          label: "IP地址",
          sortable: true,
          sortMethod: this.$tools.IPSort,
          prop: "ip",
          second_head: {
            isTemplate: true,
            templateName: "ip",
            prop: "ip",
            templateHeadName: "ip_head",
            width: "200px"
          }
        },
        {
          label: "系统类别",
          prop: "os",
          sortable: true,
          second_head: {
            width: "180px",
            isTemplate: true,
            templateName: "os",
            templateHeadName: "os_head",
            prop: "os"
          }
        },
        {
          label: "功能类别",
          prop: "tag",
          sortable: true,
          second_head: {
            isTemplate: true,
            templateName: "tag",
            templateHeadName: "tag_head",
            prop: "tag",
            minWidth: "150px"
          }
        },
        {
          label: "状态",
          prop: "online",
          sortable: true,
          second_head: {
            isTemplate: true,
            templateName: "online",
            templateHeadName: "online_sel",
            width: "150px",
            prop: "online"
          }
        },
        {
          label: "自动学习状态",
          prop: "learn_status",
          sortable: true,
          second_head: {
            isTemplate: true,
            templateName: "learn_status",
            templateHeadName: "learn_status_sel",
            width: "150px",
            prop: "learn_status"
          }
        },
        // {
        //   label: "自学习管理",
        //   isTemplate: true,
        //   templateName: "operation",
        //   second_head: {
        //     isTemplate: true,
        //     templateName: "operation",
        //     width: "330px"
        //   }
        // },
        // {
        //   label: "规则管理",
        //   isTemplate: true,
        //   templateName: "operation1",
        //   second_head: {
        //     isTemplate: true,
        //     templateName: "operation1",
        //     width: "190px"
        //   }
        // },
        // {
        //   label: "日志管理",
        //   second_head: {
        //     isTemplate: true,
        //     templateName: "netfilterlog",
        //     width: "180px",
        //     templateHeadName: "netfilterlog_head"
        //   }
        // },
        // {
        //   label: "主机管理",
        //   isTemplate: true,
        //   templateName: "operation3",
        //   width: "80px",
        //   second_head: {
        //     isTemplate: true,
        //     templateName: "operation3",
        //     width: "80px"
        //   }
        // },
        {
          type: "expand",
          fixed: "right",
          width: "60px",
          isTemplate: true,
          templateName: "expand"
        }
      ],
      tableHeadShow: true,
      params: {
        page: 1,
        limit: 10,
        name: "",
        ip: "",
        online: "",
        learn_status: "",
        os: ""
      },
      pageParams: {
        layout: "total, sizes, prev, pager, next, jumper",
        pageAlign: "left",
        pageSizes: [10, 20, 30]
      },
      icon: {
        osIcon: this.$icons.osIcon,
        stateIcon: this.$icons.stateIcon,
        linkIcon: this.$icons.linkIcon,
        tagIcon: this.$icons.tagIcon
      },
      select: {
        // 状态
        online: [
          {
            id: 1,
            name: "在线"
          },
          {
            id: 0,
            name: "离线"
          }
        ],
        // 自动学习状态
        self_learn: [
          {
            id: 1,
            name: "学习中"
          },
          {
            id: 0,
            name: "结束学习"
          }
        ]
      },
      selectData: []
    };
  },
  mixins: [moduleRefresh("hostservice_netfilter_table")],
  methods: {
    blInit(item){
      var _this = this;
      let ids = "";
      if (Array.isArray(item)) {
        if (this.$tools.isArrayNull(item)) {
          this.$message({
            type: "error",
            message: "未选中任何项!请先选择!"
          });
          return;
        }
        ids = this.$tools.mergeId(item);
      } else {
        ids = item.id;
      }
      this.$ajax({
        url: "host/netfilter/moreinitialization",
        type: "post",
        data: {
          host_id:ids
        }
      })
        .then(function(res) {
          _this.reload();
          _this.$message({
            type: "success",
            message: "批量初始化成功!"
          });
        })
        .catch(function() {});
    },
    addModule() {
      this.$layer.iframe({
        content: {
          content: addModule,
          parent: this,
          data: {
            moduleType: "netfilter",
            tipText: "hostservice_netfilter"
          }
        },
        area: ["700px", "auto"],
        title: "添加主机 ( 列表中将自动过滤与本模块冲突的资产 )",
        shadeClose: false
      });
    },
    removeModule(item) {
      let ids = "";
      if (Array.isArray(item)) {
        if (this.$tools.isArrayNull(item)) {
          this.$message({
            type: "error",
            message: "请选择需要移除的主机!"
          });
          return;
        }
        ids = this.$tools.mergeId(item);
      } else {
        ids = item.id;
      }
      this.$isShowTip(
        "hostservice",
        "hostservice_netfilter",
        "确定移除吗?",
        () => {
          this.__delModule({ host_id: ids, module: "netfilter" });
        },
        this
      );
    },
    __delModule(param) {
      delModule(param)
        .then(res => {
          this.$message({
            type: "success",
            message: "移除成功"
          });
          this.reload();
        })
        .catch(err => {});
    },
    statistical(item) {
      this.$layer.iframe({
        content: {
          content: lineBrowse,
          parent: this,
          data: {
            select_data: ""
          }
        },
        area: ["900px", "auto"],
        title: "端口访问图表",
        shadeClose: false
      });
    },
    createExportTask(item) {
      let ids = "";
      if (Array.isArray(item)) {
        if (this.$tools.isArrayNull(item)) {
          this.$message({
            type: "error",
            message: "请选择选项!"
          });
          return;
        }
        ids = this.$tools.mergeId(item);
      } else {
        ids = item.id.toString();
      }
      this.$layer.iframe({
        content: {
          content: createExportTask,
          parent: this,
          data: {
            host_id: ids
          }
        },
        area: ["500px", "auto"],
        title: "创建导出日志任务",
        shadeClose: false
      });
    },
    exportTaskList() {
      this.$layer.iframe({
        content: {
          content: exportLogTaskList,
          parent: this,
          data: {}
        },
        area: ["800px", "auto"],
        title: "导出日志任务下载列表",
        shadeClose: false
      });
    },
    setLogOptions(item) {
      let ids = "";
      let isBatch = false;
      if (Array.isArray(item)) {
        if (this.$tools.isArrayNull(item)) {
          this.$message({
            type: "error",
            message: "请选择选项!"
          });
          return;
        }
        isBatch = true;
        ids = this.$tools.mergeId(item);
      } else {
        isBatch = false;
        ids = item.id.toString();
      }
      this.$layer.iframe({
        content: {
          content: setLogOptions,
          parent: this,
          data: {
            host_id: ids,
            isBatch: isBatch
          }
        },
        area: ["500px", "auto"],
        title: "设置日志选项",
        shadeClose: false
      });
    },
    hitWaring(item) {
      this.$layer.iframe({
        content: {
          content: hitAlarm,
          parent: this,
          data: {
            layer_data: item
          }
        },
        area: ["1000px", "auto"],
        title: "虚拟规则实时命中告警 ( 该功能仅在自学习功能的学习期内有效 )",
        shadeClose: false
      });
    },
    realWarning(item) {
      this.$layer.iframe({
        content: {
          content: realAlarm,
          parent: this,
          data: {
            layer_data:item
          }
        },
        area: ["1200px", "auto"],
        title: "真实规则实时命中告警",
        shadeClose: false
      });
    },
    showLog(item) {
      this.$layer.iframe({
        content: {
          content: showLogList,
          parent: this,
          data: {
            layer_data: item,
            isIgnore: 0
          }
        },
        area: ["850px", "auto"],
        title: "查看日志",
        shadeClose: false
      });
    },
    showIngoreLog(item) {
      this.$layer.iframe({
        content: {
          content: showLogList,
          parent: this,
          data: {
            layer_data: item,
            isIgnore: 1
          }
        },
        area: ["850px", "auto"],
        title: "查看被忽略日志",
        shadeClose: false
      });
    },
    openLog(item) {
      this.$isShowTip(
        "hostservice",
        "hostservice_netfilter",
        "确定开启日志吗？",
        () => {
          this.__logOpen({
            id: item.id
          });
        },
        this
      );
    },
    closeLog(item) {
      this.$isShowTip(
        "hostservice",
        "hostservice_netfilter",
        "确定关闭日志吗？",
        () => {
          this.__logClose({
            id: item.id
          });
        },
        this
      );
    },
    __logOpen(param) {
      return logOpen(param)
        .then(res => {
          this.$message({
            type: "success",
            message: "开启成功"
          });
          this.reload();
        })
        .catch(err => {});
    },
    __logClose(param) {
      return logClose(param)
        .then(res => {
          this.$message({
            type: "success",
            message: "关闭成功"
          });
          this.reload();
        })
        .catch(err => {});
    },
    // 点击开始学习
    study_turn(item) {
      let _this = this;
      var study_inner_val = "";
      if (item.netfilter.learn_status == 0) {
        study_inner_val = "开始学习";
      } else {
        study_inner_val = "停止学习";
      }
      // 开始学习
      if (study_inner_val == "开始学习") {
        this.$ajax({
          url: "host/netfilter/learn/on",
          type: "post",
          data: {
            host_id: item.id
          }
        })
          .then(function(res) {
            _this.reload();
            _this.$message({
              type: "success",
              message: "已经开始学习!"
            });
          })
          .catch(function() {});
      } else if (study_inner_val == "停止学习") {
        this.$ajax({
          url: "host/" + item.id + "/netfilter/learn/off",
          type: "post",
          data: {}
        })
          .then(function(res) {
            _this.$message({
              type: "success",
              message: "已经停止学习!"
            });
            _this.reload();
          })
          .catch(function() {});
      }
    },
    // 判断是否有选中项
    selectChange(select) {
      this.selectData = select;
    },
    // 初始化
    initialization(item) {
      var _this = this;
      this.$ajax({
        url: "host/" + item.id + "/netfilter/initialization",
        type: "post",
        data: {
          os: item.os
        }
      })
        .then(function(res) {
          _this.reload();
          _this.$message({
            type: "success",
            message: "已初始化!"
          });
        })
        .catch(function() {});
    },
    search() {
      this.params.page = 1;
      this.reload();
    },
    // 批量学习
    bl(item) {
      let ids = "";
      if (Array.isArray(item)) {
        if (this.$tools.isArrayNull(item)) {
          this.$message({
            type: "error",
            message: "未选中任何项!请先选择!"
          });
          return;
        }
        ids = this.$tools.mergeId(item);
      } else {
        ids = item.id;
      }
      this.$layer.iframe({
        content: {
          content: bl,
          parent: this,
          data: {
            host_id: ids
          }
        },
        area: ["550px", "auto"],
        title: "批量学习",
        shadeClose: false
      });
    },
    exRule() {
      //下载导出规则
      this.$layer.iframe({
        content: {
          content: exRule,
          parent: this,
          data: {
            select_data: ""
          }
        },
        area: ["900px", "auto"],
        title: "导出规则下载列表",
        shadeClose: false
      });
    },
    manualRule(item) {
      //  批量下发手工规则
      let ids = "";
      if (Array.isArray(item)) {
        if (this.$tools.isArrayNull(item)) {
          this.$message({
            type: "error",
            message: "未选中任何项!请先选择!"
          });
          return;
        }
        ids = this.$tools.mergeId(item);
      } else {
        ids = item.id;
      }
      var ips = [];
      for (var tag of item) {
        ips.push(tag.ip);
      }
      var ipstr = ips.join(",");
      this.$layer.iframe({
        content: {
          content: manualRule,
          parent: this,
          data: {
            host_id: ids,
            ipstr: ipstr
          }
        },
        area: ["620px", "650px"],
        title: "批量下发手工规则",
        shadeClose: false
      });
    },
    // 批量导出规则
    export_rule(item) {
      let _this = this;
      let ids = "";
      if (Array.isArray(item)) {
        if (this.$tools.isArrayNull(item)) {
          this.$message({
            type: "error",
            message: "未选中任何项!请先选择!"
          });
          return;
        }
        ids = this.$tools.mergeId(item);
      } else {
        ids = item.id;
      }
      this.$isShowTip(
        "hostservice",
        "hostservice_netfilter",
        "确定批量导出规则吗?",
        () => {
          // 操作 请求
          this.$ajax({
            url: "download/create/2",
            type: "post",
            data: {
              host_id: ids
            }
          })
            .then(function(res) {
              _this.$message({
                type: "success",
                message: "操作成功!"
              });
              _this.reload();
            })
            .catch(function() {});
        },
        this
      );
    },
    LearningConfiguration(item) {
      //  自学习配置
      this.$layer.iframe({
        content: {
          content: LearningConfiguration,
          parent: this,
          data: {
            host_id: item.id
          }
        },
        area: ["550px", "auto"],
        title: "自学习配置",
        shadeClose: false
      });
    },
    autodateLearning_one(item) {
      //  自学习数据
      this.$layer.iframe({
        content: {
          content: autodateLearning_one,
          parent: this,
          data: {
            host_id: item.id
          }
        },
        area: ["850px", "auto"],
        title: "自学习数据",
        shadeClose: false
      });
    },
    autoRuleLearning(item) {
      //  自学习规则
      this.$layer.iframe({
        content: {
          content: autoRuleLearning,
          parent: this,
          data: {
            host_id: item.id
          }
        },
        area: ["1750px", "auto"],
        title: "自学习规则",
        shadeClose: false
      });
    },
    defaultRule(item) {
      //  默认规则
      this.$layer.iframe({
        content: {
          content: defaultRule,
          parent: this,
          data: {
            host_id: item.id
          }
        },
        area: ["680px", "auto"],
        title: "默认规则",
        shadeClose: false
      });
    },
    // 原始规则
    originalRule(item) {
      this.$layer.iframe({
        content: {
          content: originalRule,
          parent: this,
          data: {
            host_id: item.id
          }
        },
        area: ["680px", "auto"],
        title: "原始规则",
        shadeClose: false
      });
    },
    deploy(item) {
      //  配置
      this.$layer.iframe({
        content: {
          content: deploy,
          parent: this,
          data: {
            host_id: item.id,
            firewall: item.netfilter.firewall
          }
        },
        area: ["622px", "auto"],
        title: "配置",
        shadeClose: false
      });
    },
    manRule(item) {
      //  人工规则
      this.$layer.iframe({
        content: {
          content: manRule,
          parent: this,
          data: {
            item: item,
            host_id: item.id
          }
        },
        area: ["1660px", "auto"],
        title: " 人工规则",
        shadeClose: false
      });
    },
    fictitious_Rule(item) {
      //  虚拟规则
      this.$layer.iframe({
        content: {
          content: fictitious_Rule,
          parent: this,
          data: {
            item: item,
            host_id: item.id
          }
        },
        area: ["1840px", "auto"],
        title: " 虚拟规则 ( 该功能仅在自学习功能开启时的学习期内有效 )",
        shadeClose: false
      });
    }
  }
};
</script>

<style scoped>
.netfilter_btn_width {
  width: 120px;
}
.hostservice-netfilter .tagIcon-notice {
  /* height: 50px; */
  line-height: 50px;
}
.hostservice-netfilter .tagIcon-notice span {
  margin-right: 10px;
}
.hostservice-netfilter .tagIcon-notice svg {
  vertical-align: middle;
}
.hostservice-netfilter .ahsmp-table-expand .el-form-item {
  width: 100%;
  margin-right: 0;
  margin-bottom: 0;
  line-height: 1;
}
</style>



